/**
 * 消息推送 
 */
<template>
  <div>
    <!-- 面包屑导航 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>消息推送任务</el-breadcrumb-item>
    </el-breadcrumb>
    <el-form class="user-search">
      <el-form-item>
        <el-button size="small" type="primary" icon="el-icon-plus" @click="handleAction({action:'创建'})">创建推送任务</el-button>
      </el-form-item>
    </el-form>
    <!--列表-->
    <el-table size="small" :data="listData" highlight-current-row v-loading="loading" border element-loading-text="拼命加载中" style="width: 100%;">
      <el-table-column  prop="type" label="消息类型" width="80"></el-table-column>
      <el-table-column prop="title" label="推送标题" width="80"></el-table-column>
      <el-table-column prop="content" label="消息内容" width="250"></el-table-column>
      <el-table-column sortable prop="push_time" label="推送时间" width="140"></el-table-column>
      <el-table-column prop="push_status" label="推送状态" width="80"></el-table-column>
      <el-table-column prop="push_users" label="推送对象"></el-table-column>
      <el-table-column prop="time_modify" label="更新时间" width="140">
        <template slot-scope="scope">
          <div>{{scope.row.time_modify|timestampToTime}}</div>
        </template>
      </el-table-column>
      <el-table-column align="center" label="操作" width="200">
        <template slot-scope="scope" >
          <el-button type="danger" size="mini" @click="handleAction(scope.row)">{{scope.row.action}}</el-button>
        </template>
      </el-table-column>
    </el-table>
    <!-- 编辑界面 -->
      <el-dialog title="编辑推送" :visible.sync="dialogType['editMessage']" width="60%" >
        <el-form label-width="120px" :model="ruleForm.editMessage" :rules="rules.editMessage" ref="editMessageForm">
          <el-form-item label="设备类型" prop="device_type">
            <el-radio-group v-model="ruleForm.editMessage.device_type" disabled>
              <el-radio :label="1">Android</el-radio>
              <el-radio :label="2">IOS</el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item label="消息类型" prop="type" >
            <el-select size="small" v-model="ruleForm.editMessage.type" placeholder="请选择" disabled>
              <el-option
              v-for="item in listType"
              :key="item.id"
              :label="item.name"
              :value="item.id">
            </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="推送标题" prop="title">
            <el-input size="small" v-model="ruleForm.editMessage.title" auto-complete="off" placeholder="请输入标题"></el-input>
          </el-form-item>
          <el-form-item label="消息内容" prop="content">
            <el-input
              type="textarea"
              autosize
              placeholder=""
              :autosize="{ minRows: 2, maxRows: 4}"
              v-model="ruleForm.editMessage.content">
            </el-input>
          </el-form-item>
          <el-form-item label="推送时间" prop="push_time">
             <el-date-picker
              v-model="ruleForm.editMessage.push_time"
              type="datetime"
              format="yyyy-MM-dd HH:mm:ss"
              value-format="yyyy-MM-dd HH:mm:ss"
              :picker-options="pickerOptions1"
              placeholder="请选择日期">
            </el-date-picker>
          </el-form-item>
         <el-form-item label="目标人群" prop="push_users">
            <el-radio-group v-model="ruleForm.editMessage.push_users_type" @change="changeUsersType('editMessage')">
              <el-radio :label="1">全部用户</el-radio>
              <el-radio :label="2">部分用户</el-radio>
              <el-radio :label="3">独立用户</el-radio>
            </el-radio-group>
            <template v-if="ruleForm.editMessage.push_users_type==2">
              <div>普通筛选条件:</div>
              <div>
                <div>
                  标    签：
                  <el-select v-model="ruleForm.editMessage.labels" multiple placeholder="请选择">
                    <el-option
                      v-for="item in listLabels"
                      :key="item.id"
                      :label="item.name"
                      :value="item.id">
                    </el-option>
                  </el-select>
                </div>
                <div>
                  省   份：
                  <el-select v-model="ruleForm.editMessage.province" multiple placeholder="请选择">
                    <el-option
                      v-for="item in listProvince"
                      :key="item.id"
                      :label="item.name"
                      :value="item.id">
                    </el-option>
                  </el-select>
                </div>
                <div>
                  活跃度：
                  <el-select v-model="ruleForm.editMessage.activity" placeholder="请选择">
                    <el-option
                      v-for="item in listActivity"
                      :key="item.id"
                      :label="item.name"
                      :value="item.id">
                    </el-option>
                  </el-select>
                </div>
              </div>
            </template>
            <template v-if="ruleForm.editMessage.push_users_type==3">
              <el-input v-model="ruleForm.editMessage.push_users" auto-complete="off">
                <template slot="prepend">Device Token</template>
              </el-input>
              <!-- <el-row :gutter="24">
                <el-col :span="18">
                  <el-input v-model="ruleForm.editMessage.push_users" auto-complete="off">
                    <template slot="prepend">Device Token</template>
                  </el-input>
                </el-col>
                <el-col :span="6">
                  <el-input v-model="ruleForm.editMessage.push_users" auto-complete="off">
                    <el-button slot="append" icon="el-icon-search"></el-button>
                  </el-input>
                </el-col>
              </el-row> -->
            </template>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button size="small" @click="cancelForm('editMessageForm', 'editMessage')">取消</el-button>
          <el-button size="small" type="primary" @click="submitForm('editMessageForm', 'editMessage')">保存</el-button>
        </div>
      </el-dialog>
      <!-- 新增界面 -->
      <el-dialog title="新增推送" :visible.sync="dialogType['addMessage']" width="60%" >
        <el-form label-width="120px" :model="ruleForm.addMessage":rules="rules.addMessage" ref="addMessageForm">
          <el-form-item label="设备类型" prop="device_type">
            <el-radio-group v-model="ruleForm.addMessage.device_type">
              <el-radio :label="1">Android</el-radio>
              <el-radio :label="2">IOS</el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item label="消息类型" prop="type">
            <el-select size="small" v-model="ruleForm.addMessage.type" placeholder="请选择">
              <el-option
              v-for="item in listType"
              :key="item.id"
              :label="item.name"
              :value="item.id">
            </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="推送标题" prop="title">
            <el-input size="small" v-model="ruleForm.addMessage.title" auto-complete="off" placeholder="请输入标题"></el-input>
          </el-form-item>
          <el-form-item label="消息内容" prop="content">
            <el-input
              type="textarea"
              autosize
              placeholder=""
              :autosize="{ minRows: 2, maxRows: 4}"
              v-model="ruleForm.addMessage.content">
            </el-input>
          </el-form-item>
          <el-form-item label="推送时间" prop="push_time">
             <el-date-picker
              v-model="ruleForm.addMessage.push_time"
              type="datetime"
              format="yyyy-MM-dd HH:mm:ss"
              value-format="yyyy-MM-dd HH:mm:ss"
              start-placeholder="2019-07-09 00:00:00"
              :picker-options="pickerOptions1"
              placeholder="请选择日期">
            </el-date-picker>
          </el-form-item>
          <el-form-item label="目标人群" prop="push_users">
            <el-radio-group v-model="ruleForm.addMessage.push_users_type" @change="changeUsersType('addMessage')">
              <el-radio :label="1">全部用户</el-radio>
              <el-radio :label="2">部分用户</el-radio>
              <el-radio :label="3">独立用户</el-radio>
            </el-radio-group>
            <template v-if="ruleForm.addMessage.push_users_type==2">
              <div>普通筛选条件:</div>
              <div>
                <div>
                  标    签：
                  <el-select v-model="ruleForm.addMessage.labels" multiple placeholder="请选择">
                    <el-option
                      v-for="item in listLabels"
                      :key="item.id"
                      :label="item.name"
                      :value="item.id">
                    </el-option>
                  </el-select>
                </div>
                <div>
                  省   份：
                  <el-select v-model="ruleForm.addMessage.province" multiple placeholder="请选择">
                    <el-option
                      v-for="item in listProvince"
                      :key="item.id"
                      :label="item.name"
                      :value="item.id">
                    </el-option>
                  </el-select>
                </div>
                <div>
                  活跃度：
                  <el-select v-model="ruleForm.addMessage.activity" placeholder="请选择">
                    <el-option
                      v-for="item in listActivity"
                      :key="item.id"
                      :label="item.name"
                      :value="item.id">
                    </el-option>
                  </el-select>
                </div>
              </div>
            </template>
            <template v-if="ruleForm.addMessage.push_users_type==3">
              <el-input v-model="ruleForm.addMessage.push_users" auto-complete="off">
                <template slot="prepend">Device Token</template>
              </el-input>
              <!-- <el-row :gutter="24">
                <el-col :span="18">
                  <el-input v-model="ruleForm.addMessage.push_users" auto-complete="off">
                    <template slot="prepend">Device Token</template>
                  </el-input>
                </el-col>
                <el-col :span="6">
                  <el-input v-model="ruleForm.addMessage.push_users" auto-complete="off">
                    <el-button slot="append" icon="el-icon-search"></el-button>
                  </el-input>
                </el-col>
              </el-row> -->
            </template>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button size="small" @click="cancelForm('addMessageForm', 'addMessage')">取消</el-button>
          <el-button size="small" type="primary" @click="submitForm('addMessageForm', 'addMessage')">保存</el-button>
        </div>
      </el-dialog>
    <!-- 分页组件 -->
    <Pagination v-bind:child-msg="pageparm" @callFather="callFather" ></Pagination>
  </div>
</template>

<script>
import {
  messagePushDo
} from '../../api/apiConfig'
import Pagination from '../../components/Pagination'
export default {
  data() {
    return {
      formInline: {
        keywords:"",
        token: localStorage.getItem('logintoken'),
      },
      listData: [],
      dialogType: {
        'addMessage': false,
        'editMessage': false,
      },
      ruleForm: {
        addMessage: {
          device_type: 1,
          type: 1,
          title: '',
          content: '',
          push_time: '',
          push_status: 1,
          push_users: '@all',
          push_users_type: 1,
          link: '',
          labels: '',
          province: '',
          activity: '',
        },
        editMessage: {
          id: 0,
          device_type: 1,
          type: 1,
          title: '',
          content: '',
          push_time: '',
          push_status: 1,
          push_users: '',
          push_users_type: '',
          time_modify: '',
          time_create: '',
          link:'',
          labels: '',
          province: '',
          activity: '',
        }
      },
      rules: {
        addMessage: {
          content: [
            {required: true, message: '推送内容必填', trigger: 'change'}
          ],
          push_users: [
            // {required: true, message: '推送对象必填', trigger: 'change'}
          ]
        },
        editMessage: {
          content: [
            {required: true, message: '推送内容必填', trigger: 'change'}
          ],
          push_users: [
            // {required: true, message: '推送对象必填', trigger: 'change'}
          ]
        }
      },
      listLabels:[
        {id: 1,name: "时尚"},
        {id: 2,name: "民族风"},
        {id: 3,name: "简约"},
        {id: 4,name: "科技"},
      ],
      listProvince:[
        {id: 1,name: "广东"},
        {id: 2,name: "广西"},
        {id: 3,name: "北京"},
        {id: 4,name: "上海"},
        {id: 5,name: "江苏"},
      ],
      listActivity:[
        {id: 1,name: "最近活跃"},
        {id: 2,name: "不活跃"},
        {id: 3,name: "近一周活跃"},
        {id: 4,name: "近一个月活跃"},
      ],
      listType:[
        {id: 1,name: "文本"},
        {id: 2,name: "应用页"},
        {id: 3,name: "H5页"}
      ],
      // 分页参数
      pageparm: {
        currentPage: 1,
        pageSize: 20,
        total: 10
      },
      pickerOptions1:{
        disabledDate(time) {
          return time.getTime() < (Date.now()-24*60*60*1000);
        }
      }
    }
  },
  // 注册组件
  components: {
    Pagination
  },
  /**
   * 数据发生改变
   */

  /**
   * 创建完毕
   */
  created() {
    this.getdata(this.formInline)
  },

  /**
   * 里面的方法只有被调用才会执行
   */
  methods: {
    // 获取管理员列表
    getdata(parameter) {
      this.loading = true
      /***
       * 调用接口
       */
      messagePushDo(parameter)
        .then(res => {
          this.loading = false
          //登录过期
          if(res.errcode == 10006){
            this.$store.commit('logout', 'false')
            this.$router.push({ path: '/login' })
            return false
          }
          if (res.errcode === 0) {
            let data = res.data.datalist;
            /*Object.keys(data).forEach(function (key) {
              data[key]['action'] = '编辑'
            })*/
             this.listData = data
            // 分页赋值
            this.pageparm.currentPage = res.data.current_page
            this.pageparm.total = res.data.total_count
          } else {
            this.$message({
              type: 'info',
              message: res.errmsg
            })
          }
        })
        .catch(err => {
          this.loading = false
          console.log('err:',err)
          this.$message.error('数据加载失败，请稍后再试！')
        })
    },
    // 分页插件事件
    callFather(parm) {
      this.formInline.page = parm.currentPage
      this.formInline.pagesize = parm.pageSize
      this.getdata(this.formInline)
    },
    changeUsersType(action){
      if(action='addMessage'){
        this.ruleForm['addMessage']['push_users'] = this.ruleForm['addMessage']['push_users_type']==1 ? '@all' : ''
      }
      if(action='editMessage'){
        this.ruleForm['editMessage']['push_users'] = this.ruleForm['editMessage']['push_users_type']==1 ? '@all' : ''
      }
    },
    // 点击操作显示对应的对话框
    handleAction(msg) {
      if (msg.action == '创建') {
        this.dialogType['addMessage'] = true
      }
      if (msg.action == '编辑') {
        this.ruleForm['editMessage']['id'] = msg.id
        this.ruleForm['editMessage']['title'] = msg.title
        this.ruleForm['editMessage']['id'] = msg.id
        this.ruleForm['editMessage']['content'] = msg.content
        this.ruleForm['editMessage']['push_time'] = msg.push_time
        this.ruleForm['editMessage']['push_status'] = msg.push_status
        this.ruleForm['editMessage']['push_users'] = msg.push_users
        this.ruleForm['editMessage']['time_modify'] = msg.time_modify
        this.ruleForm['editMessage']['time_create'] = msg.time_create
        this.ruleForm['editMessage']['link'] = msg.link

        this.dialogType['editMessage'] = true
      }
    },
    cancelForm(){
      this.dialogType['addMessage'] = false
      this.dialogType['editMessage'] = false
    },
    submitForm(formName, type) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          if (type == 'addMessage') {
            //创建
            let ajaxData = this.ruleForm['addMessage']
            ajaxData['act'] = 'add'
            //数据校验

            messagePushDo(ajaxData).then(res => {
              if (res.errcode===0) {
                this.$message({
                  type: 'success',
                  message: res.errmsg
                })
                this.getdata(this.formInline)
              } else {
                this.$message({
                  type: 'info',
                  message: res.errmsg
                })
              }
            })
            .catch(err => {
              this.loading = false
              this.$message.error('操作，请稍后再试！')
            })
          }
          if (type == 'editMessage') {
            //编辑
            let ajaxData = this.ruleForm['editMessage']
            ajaxData['act'] = 'update'
            messagePushDo(ajaxData).then(res => {
              if (res.errcode===0) {
                this.$message({
                  type: 'success',
                  message: res.errmsg
                })
                this.getdata(this.formInline)
              } else {
                this.$message({
                  type: 'info',
                  message: res.errmsg
                })
              }
            })
            .catch(err => {
              this.loading = false
              this.$message.error('操作，请稍后再试！')
            })
          }
          this.dialogType[type] = false
        } else {
          console.log('error submit!!');
          return false;
        }
      });
    },
  }
}
</script>

<style scoped>
.user-search {
  margin-top: 20px;
}
.userRole {
  width: 100%;
}
.table_img{
  width: 35px;
  height: 35px;
}
</style>

 



