// 导入组件
import Vue from 'vue';
import Router from 'vue-router';
// 登录
import login from '@/views/login';
// 首页
import index from '@/views/index';
/**
 * 系统管理
 */
import guide from '@/views/guides/guide';// 指引页管理
import banner from '@/views/banner/banner';// banner页管理
import banner_china_girl from '@/views/banner/china_girl';// 中国女孩banner页管理
import first_news from '@/views/News/first_news';//首页栏目
import hotsearch from '@/views/common/hotsearch';//热搜

/**
 *资讯管理
 */
// 资讯管理信息
import News from '@/views/News/News';//资讯列表
import editNews from '@/views/News/editNews';//资讯编辑、新增
import news_type from '@/views/News/news_type';//资讯类别
import news_comment from '@/views/News/news_comment';//资讯评论


/**
 * 社区内容管理
 */

// 社区内容界面
import talks from '@/views/talks/talks';
import talk_detail from '@/views/talks/talk_detail';
import talklabel from '@/views/talks/talklabel';

/**
 * 管理员管理
 */

// 管理员界面
import admin from '@/views/admin/admin';
import user from '@/views/admin/user';

//消息推送管理
import message_push from '@/views/message/message_push';
import member_device from '@/views/message/member_device';

// 其他管理
import feedback from '@/views/other/feedback';
import eport from '@/views/other/eport';


// 启用路由
Vue.use(Router);

// 导出路由 
export default new Router({
    routes: [{
        path: '/',
        name: '',
        component: login,
        hidden: true,
        meta: {
            requireAuth: false
        }
    }, 
    {
        path: '/login',
        name: '登录',
        component: login,
        hidden: true,
        meta: {
            requireAuth: false
        }
    }, 
    {
        path: '/index',
        name: '首页',
        component: index,
        iconCls: 'el-icon-tickets',
        children: [
        {
            path: '/guides/guide',
            name: '引导图管理',
            component: guide,
            meta: {
                requireAuth: true
            }
        }, 
        {
            path: '/banner/banner',
            name: 'banner管理',
            component: banner,
            meta: {
                requireAuth: true
            }
        },
        {
            path: '/banner/china_girl',
            name: '中国女孩banner管理',
            component: banner_china_girl,
            meta: {
                requireAuth: true
            }
        },
        {
            path: '/News/News',
            name: '资讯信息管理',
            component: News,
            meta: {
                requireAuth: true
            }
        }, 
        {
            path: '/admin/admin',
            name: '管理员界面',
            component: admin,
            meta: {
                requireAuth: true
            }
        },
        {
            path: '/News/editNews',
            name: '资讯编辑页面',
            component: editNews,
            meta: {
                requireAuth: true
            }
        },
        {
            path: '/talks/talks',
            name: '社区内容页面',
            component: talks,
            meta: {
                requireAuth: true
            }
        },
        {
            path: '/talks/talk_detail',
            name: '社区内容详情',
            component: talk_detail,
            meta: {
                requireAuth: true
            }
        },
        {
            path: '/News/news_type',
            name: '资讯类别',
            component: news_type,
            meta: {
                requireAuth: true
            }
        },
        {
            path: '/News/news_comment',
            name: '资讯评论列表',
            component: news_comment,
            meta: {
                requireAuth: true
            }
        },
        {
            path: '/News/first_news',
            name: '首页栏目',
            component: first_news,
            meta: {
                requireAuth: true
            }
        },
        {
            path: '/common/hotsearch',
            name: '热搜榜',
            component: hotsearch,
            meta: {
                requireAuth: true
            }
        },
        {
            path: '/admin/user',
            name: '会员列表',
            component: user,
            meta: {
                requireAuth: true
            }
        },
        {
            path: '/talks/talklabel',
            name: '社区基础标签',
            component: talklabel,
            meta: {
                requireAuth: true
            }
        },
        {
            path: '/other/feedback',
            name: '反馈建议',
            component: feedback,
            meta: {
                requireAuth: true
            }
        },
        {
            path: '/other/eport',
            name: '内容举报',
            component: eport,
            meta: {
                requireAuth: true
            }
        },
        {
            path: '/message/message_push',
            name: '消息推送',
            component: message_push,
            meta: {
                requireAuth: true
            }
        },
        {
            path: '/message/member_device',
            name: '用户设备',
            component: member_device,
            meta: {
                requireAuth: true
            }
        }

        ]
    }]
})


