/**
 * 管理员管理 
 */
<template>
  <div>
    <!-- 面包屑导航 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>管理员管理</el-breadcrumb-item>
    </el-breadcrumb>

    <!-- <el-form class="user-search">
      <el-form-item>
        <el-button size="small" type="primary" icon="el-icon-plus" @click="handleAddd()">添加</el-button>
      </el-form-item>
    </el-form> -->
    <!-- 搜索筛选 -->
    <el-form :inline="true" :model="formInline" class="user-search">
      <el-form-item label="搜索：">
        <el-input size="small" v-model="formInline.keywords" placeholder="输入账号或昵称"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button size="small" type="primary" icon="el-icon-search" @click="search">搜索</el-button>
      </el-form-item>
    </el-form>
    <!--列表-->
    <el-table size="small" :data="listData" highlight-current-row v-loading="loading" border element-loading-text="拼命加载中" style="width: 100%;">
      <el-table-column prop="id" label="用户ID" width="80">
      </el-table-column>
      <el-table-column prop="name" label="昵称" width="100">
      </el-table-column>
      <el-table-column  prop="phone" label="账号">
      </el-table-column>
      <el-table-column prop="headimg" label="头像" width="80">
        <div  slot-scope="scope">
          <img class="table_img" :src="scope.row.headimg" alt=""/>
        </div>
      </el-table-column>
      <el-table-column sortable prop="sex" label="性别" width="80">
      </el-table-column>
      <el-table-column prop="country" label="国家" width="100">
      </el-table-column>
      <el-table-column sortable prop="province" label="省份" width="100">
      </el-table-column>
      <el-table-column sortable prop="city" label="城市" width="100">
      </el-table-column>
      <el-table-column sortable prop="age" label="年龄" width="80">
      </el-table-column>
      <!-- <el-table-column sortable prop="weibo" label="博客" width="100"></el-table-column> -->
      <!-- <el-table-column sortable prop="role" label="角色" width="80"></el-table-column> -->
      <el-table-column sortable prop="time_create" label="新增时间">
        <template slot-scope="scope">
          <div>{{scope.row.time_create|timestampToTime}}</div>
        </template>
      </el-table-column>
      <el-table-column align="center" label="操作" min-width="">
        <template slot-scope="scope">
          <el-button size="mini" type="danger" @click="deleteUser(scope.$index, scope.row)">取消管理身份</el-button>
        </template>
      </el-table-column>
    </el-table>
    <!-- 新增界面 -->
    <el-dialog :title="title" :visible.sync="addFormVisible" width="30%" @click="closeDialog">
      <el-form label-width="120px" :model="addForm" :rules="rules" ref="editForm">
        <el-form-item label="名称" prop="name">
          <el-input size="small" v-model="addForm.name" auto-complete="off" placeholder="请输入名称"></el-input>
        </el-form-item>
        <el-form-item label="排序值" prop="sort">
          <el-input size="small" v-model="addForm.sort" auto-complete="off" placeholder="请输入排序值"></el-input>
        </el-form-item>
        <el-form-item label="状态" prop="status">
          <el-select size="small" v-model="addForm.status" placeholder="请选择" class="userRole">
            <el-option
            v-for="item in listUser"
            :key="item.id"
            :label="item.name"
            :value="item.id">
          </el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button size="small" @click="closeDialog">取消</el-button>
        <el-button size="small" type="primary" :loading="loading" class="title" @click="submitAddForm()">新增</el-button>
      </div>
    </el-dialog>

    <!-- 分页组件 -->
    <Pagination v-bind:child-msg="pageparm" @callFather="callFather"></Pagination>
  </div>
</template>

<script>
import {
  adminConfigList,
  adminDel
} from '../../api/apiConfig'
import Pagination from '../../components/Pagination'
import timestampToTime from '../../utils/util'
export default {
  data() {
    return {
      nshow: true, //switch开启
      fshow: false, //switch关闭
      loading: false, //是显示加载
      addFormVisible: false, //控制编辑页面显示与隐藏
      title: '添加',
      addForm: {
        act:"add",
        name: '',
        status: '',
        sort: '',
        token: localStorage.getItem('logintoken')
      },
      formInline: {
        keywords:"",
        token: localStorage.getItem('logintoken'),
      },
      listData: [],
      listUser: [],
      rules: {
        name: [
          { required: true, message: '请输入名称', trigger: 'blur' }
        ],
        sort: [
          { required: true, message: '请输入排序值', trigger: 'blur' }
        ],
        status: [{ required: true, message: '请选择状态', trigger: 'blur' }]
      },
      // 分页参数
      pageparm: {
        currentPage: 1,
        pageSize: 10,
        total: 10
      }
    }
  },
  // 注册组件
  components: {
    Pagination
  },
  /**
   * 数据发生改变
   */

  /**
   * 创建完毕
   */
  created() {
    this.getdata(this.formInline)
  },

  /**
   * 里面的方法只有被调用才会执行
   */
  methods: {
    // 获取管理员列表
    getdata(parameter) {
      this.loading = true;
      adminConfigList(parameter)
        .then(res => {
          this.loading = false
          //登录过期
          if(res.errcode == 10006){
            this.$store.commit('logout', 'false')
            this.$router.push({ path: '/login' })
            return false
          }
          if (res.errcode === 0) {
            this.listData = res.data.memberlist;
            // 分页赋值
            this.pageparm.currentPage = res.data.current_page
            this.pageparm.total = res.data.total_count
          }  else {
            this.$message({
              type: 'info',
              message: res.errmsg
            })
          }
        })
        .catch(err => {
          this.loading = false
          console.log('err:',err)
          this.$message.error('数据加载失败，请稍后再试！')
        })
    },
    // 分页插件事件
    callFather(parm) {
      this.formInline.page = parm.currentPage
      this.formInline.pagesize = parm.pageSize
      this.getdata(this.formInline)
    },
    // 搜索事件
    search() {
      this.getdata(this.formInline)
    },
    //显示新增界面
    handleAddd: function(index, row) {
      this.addFormVisible = true
      this.title = '添加管理员'
    },
    submitAddForm() {
      cateDo(this.addForm)
        .then(res => {
          this.addFormVisible = false
          this.loading = false
          if (res.errcode === 0) {
            this.getdata(this.formInline)
            this.$message({
              type: 'success',
              message: '新增成功！'
            })
          } else {
            this.$message({
              type: 'info',
              message: res.errmsg
            })
          }
        })
        .catch(err => {
          this.addFormVisible = false
          this.loading = false
          this.$message.error('保存失败，请稍后再试！')
        })
    },
    // 删除管理员
    deleteUser(index, row) {
        this.$confirm('确定要取消'+ row.name +'的管理身份吗?', '信息', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        })
        .then(() => {
          adminDel({id: row.id})
            .then(res => {
              if (res.errcode==0) {
                this.$message({
                  type: 'success',
                  message: '操作成功!'
                })
                this.getdata(this.formInline)
              } else {
                this.$message({
                  type: 'info',
                  message: res.errmsg
                })
              }
            })
            .catch(err => {
              this.loading = false
              this.$message.error('操作失败，请稍后再试！')
            })
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: '已取消操作'
          })
        })
    },

    // 关闭新增弹出框
    closeDialog() {
      this.addFormVisible = false
    },
    //时间格式化  
    dateFormat(row, column) {  
      var date = row[column.property];  
      if (date == undefined) {  
         return "";  
      }  
      return moment(date).format("YYYY-MM-DD HH:mm:ss");  
    }  
        
  }
}
</script>

<style scoped>
.user-search {
  margin-top: 20px;
}
.userRole {
  width: 100%;
}
.table_img{
  width: 35px;
  height: 35px;
}
</style>

 



