/**
 * 资讯管理 资讯配置信息
 */
<template>
    <div>
      <!-- 面包屑导航 -->
      <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item :to="{ path: '/News/News' }">资讯管理</el-breadcrumb-item>
        <el-breadcrumb-item>资讯编辑</el-breadcrumb-item>
      </el-breadcrumb>
      <!-- 表单部分 -->
      <div style="width: 75%;margin-top:50px">
      <el-form label-width="120px" :model="listData" ref="editForm">
        <el-form-item label="资讯标题" prop="permissionName">
          <el-input size="small" v-model="listData.title" auto-complete="off" placeholder="标题必填"></el-input>
        </el-form-item>
        <el-form-item label="标签" prop="permissionName">
          <el-input size="small" v-model="listData.labels" auto-complete="off" placeholder="逗号分隔"></el-input>
        </el-form-item>
        <el-form-item label="类型" prop="permissionName">
          <el-select v-model="listData.type" placeholder="请选择类型">
            <el-option
              v-for="titem in listType"
              :key="titem.id"
              :label="titem.name"
              :value="titem.id">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="类别" prop="permissionName">
          <el-select v-model="listData.cate_id" placeholder="请选择类别">
            <el-option
              v-for="citem in listCate"
              :key="citem.id"
              :label="citem.name"
              :value="citem.id">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="排序值" prop="permissionName">
          <el-input size="small" v-model="listData.sort" auto-complete="off" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="标题图">
          <el-input size="small" v-model="listData.imgs" auto-complete="off"></el-input>
          <el-upload
            class="upload-demo"
            ref="upload"
            action="/index.php/adm/index/upLoadImg"
            name="img"
            :limit="1"
            :on-success="handleImgsSuccess"
            :file-list="fileList1"
            :auto-upload="false"
            list-type="picture">
            <el-button slot="trigger" size="small" type="primary">选取文件</el-button>
            <el-button style="margin-left: 10px;" size="small" type="success" @click="submitUpload">上传到服务器</el-button>
            <div slot="tip" class="el-upload__tip">只能上传jpg/png文件，且不超过500kb</div>
          </el-upload>
        </el-form-item>
        <el-form-item label="详情顶图">
          <el-input size="small" v-model="listData.topimgs" auto-complete="off"></el-input>
          <el-upload
            class="upload-demo"
            ref="topupload"
            action="/index.php/adm/index/upLoadImg"
            name="img"
            :limit="1"
            :on-success="handleTopSuccess"
            :file-list="fileList2"
            :auto-upload="false"
            list-type="picture">
            <el-button slot="trigger" size="small" type="primary">选取文件</el-button>
            <el-button style="margin-left: 10px;" size="small" type="success" @click="submitTopUpload">上传到服务器</el-button>
            <div slot="tip" class="el-upload__tip">只能上传jpg/png文件，且不超过500kb</div>
          </el-upload>
        </el-form-item>
        <el-form-item label="资讯内容">
          <el-upload
            class="avatar-uploader"
            :action="serverUrl"
            name="img"
            :headers="header"
            :show-file-list="false"
            :on-success="uploadSuccess"
            :on-error="uploadError"
            :file-list="fileList3"
            :before-upload="beforeUpload">
          </el-upload>
          <div>
            <quill-editor 
            class="editor"
            v-model="content"
            ref="myQuillEditor" 
            :options="editorOption" 
            @blur="onEditorBlur($event)" @focus="onEditorFocus($event)"
            @change="onEditorChange($event)">
            </quill-editor>
            <el-button type="primary" style="margin-top:20px" @click="saveHtml">保存</el-button>
          </div>
        </el-form-item>
      </el-form>
      </div>
    </div>
</template>

<script>
import {
  getNewsList,
  NewsListEdit,
  NewscateList
} from '../../api/apiConfig'
import Pagination from '../../components/Pagination'
import { quillEditor,Quill } from 'vue-quill-editor'; //调用编辑器
import 'quill/dist/quill.core.css'
import 'quill/dist/quill.snow.css'
import 'quill/dist/quill.bubble.css'

//增加音频模块--start
const BlockEmbed = Quill.import('blots/block/embed')
const Link = Quill.import('formats/link')
class Audio extends BlockEmbed {
  static create (value) {
    let node = super.create(value)
    node.setAttribute('controls', 'controls')
    node.setAttribute('src', this.sanitize(value))
    return node
  }

  static sanitize (url) {
    return Link.sanitize(url)
  }

  static value (domNode) {
    return domNode.getAttribute('src')
  }
}

Audio.blotName = 'audio'
Audio.tagName = 'audio'

Quill.register(Audio)
//增加音频模块--end


//富文本编辑器设置
const toolbarOptions = [
  // ["bold", "italic", "underline", "strike"], // 加粗 斜体 下划线 删除线
  // ["blockquote", "code-block"], // 引用  代码块
  // [{ header: 1 }, { header: 2 }], // 1、2 级标题
  // [{ list: "ordered" }, { list: "bullet" }], // 有序、无序列表
  // [{ script: "sub" }, { script: "super" }], // 上标/下标
  // [{ indent: "-1" }, { indent: "+1" }], // 缩进
  // // [{'direction': 'rtl'}],                         // 文本方向
  // [{ size: ["small", false, "large", "huge"] }], // 字体大小
  // [{ header: [1, 2, 3, 4, 5, 6, false] }], // 标题
  // [{ color: [] }, { background: [] }], // 字体颜色、字体背景颜色
  // [{ font: [] }], // 字体种类
  // [{ align: [] }], // 对齐方式
  // ["clean"], // 清除文本格式
  [/*"link",*/ "image","video"], // 链接、图片
  ['audio']
];

export default {
  // name: 'MarkdownDemo',
  data() {
    return {
      fileList1: [],//标题图
      fileList2: [],//详情顶图
      fileList3: [],//内容图
      content: this.value,
      quillUpdateImg: false, // 根据图片上传状态来确定是否显示loading动画，刚开始是false,不显示
      height:500,
      listData:{
        id:0,
        type:1,
        cate_id:0,
        title:'',
        content:'',
        topimgs:'',
        imgs:'',
        labels:'',
        sort:1,
        status: 1,
      },
      param:{
        act:"update",
        type:1,
        cate_id:0,
        title:'',
        content:'',
        topimgs:'',
        imgs:'',
        labels:'',
        sort:1,
        status: 1,
      },
      listCate:[{id:0,name:'请选择类别'}],
      listType:[{id:1,name:'文章'},{id:3,name:'视频'},{id:2,name:'音频'}],
      editorOption: {
        placeholder: "",
        theme: "snow", // or 'bubble'
        placeholder: "请输入内容",
        modules: {
          toolbar: {
            container: toolbarOptions,
            handlers: {
              image: function(value) {
                if (value) {
                  // 触发input框选择图片文件
                  document.querySelector(".avatar-uploader input").click();
                } else {
                  this.quill.format("image", false);
                }
              },
              // audio: function(value){
              //   console.log('audio:',value)
              // }
              // link: function(value) {
              //   if (value) {
              //     var href = prompt('请输入url');
              //     this.quill.format("link", href);
              //   } else {
              //     this.quill.format("link", false);
              //   }
              // }
            }
          }
        }
      },
      serverUrl: "/index.php/adm/index/upLoadImg", // 上传的图片服务器地址
      header: {
        token: localStorage.getItem('logintoken')
      } // 有的图片服务器要求请求头需要有token
    };
  },
  // 注册组件
  components: {
    quillEditor
  },
  /**
   * 创建完毕
   */
  created() {
  },
  props: {
    /*编辑器的内容*/
    value: {
      type: String
    },
    /*图片大小*/
    maxSize: {
      type: Number,
      default: 4000 //kb
    }
  },
  computed: {
    editor() {
      return this.$refs.myQuillEditor.quill
    }
  },
  mounted () {
    // 设置编辑器高度
    this.editor.container.style.height = `${this.height}px`;
    this.pageInit();
    this.getCateList();
    this.audioButton();
  },
  /**
   * 里面的方法只有被调用才会执行
   */
  methods: {
    pageInit(){
      let list = this.$route.query
      if(list.id){
        this.listData.id = list.id
        this.getdata()
      }
    },
    getdata(){
      if(this.listData.id<1){
        return false
      }
      NewsListEdit({id:this.listData.id})
      .then(res => {
        if (res.errcode === 0) {
          let news = res.data.news;
          this.listData = {
            id: news.id,
            type: news.type,
            cate_id: news.cate_id,
            title: news.title,
            content: news.content,
            topimgs: news.topimgs,
            imgs: news.imgs,
            labels: news.labels,
            sort: news.sort,
            status:news.status,
          }
          if(news.imgs){
            this.fileList1 = [{"name":'标题图',"url":news.imgs}];
          }
          if(news.topimgs){
            this.fileList2 = [{"name":'详情顶图',"url":news.topimgs}]; 
          }
          let str = '';
          let thisContent = JSON.parse(news.content).map(v => ({ ...v, src: v. src && v.src.replace(/\\/g, "") || ''}) );
          for (let i = 0; i < thisContent.length; i++) {
            if (thisContent[i].type == 'text') {
                str = str + '<p>'+thisContent[i].text+'</p>';
            } else if(thisContent[i].type == 'img'){
                str = str + '<img src='+thisContent[i].src+ ' width='+ thisContent[i].width+ ' height='+ thisContent[i].height+'>';
            }else if(thisContent[i].type == 'video'){
                str = str + '<iframe class="ql-video" frameborder="0" allowfullscreen="true" src="'+ thisContent[i].src +'"></iframe>';
            }else if(thisContent[i].type == 'audio'){
                str = str + '<audio controls="controls" src="'+ thisContent[i].src +'"></audio>';
            }
          }
          // console.log("str：",str)
          this.content = str;
          console.log('res:',res)
        } else {
          this.$message({
            type: 'info',
            message: res.errmsg
          })
        }
      })
      .catch(err => {
        this.loading = false
        this.$message.error('数据获取失败，请稍后再试！')
      })
    },
    getCateList(){
      NewscateList(this.ruleForm).then(res => {
        if (res.errcode == 0) {
          let catelist = res.data.catelist
          let cate_temp = []
          for (var i = catelist.length - 1; i >= 0; i--) {
            if( catelist[i]['status']==1 ||this.listData.cate_id==catelist[i]['id'] ){
              cate_temp.push(catelist[i])
            }
          }
          this.listCate = cate_temp;//res.data.catelist
        } else {
          this.$message.error(res.errmsg)
        }
      })
    },
    audioButton(){
      const audioBtn = document.querySelector('.ql-audio');
      audioBtn.classList.add('fa');
      audioBtn.classList.add('fa-file-audio-o');
    },
    onEditorBlur({ quill, html, text }) {
      //失去焦点事件
    },
    onEditorFocus() {
      //获得焦点事件
    },
    onEditorChange({ quill, html, text }) {
      //内容改变事件
      this.$emit("input", this.content);
      this.content = html;
    },

    // 富文本图片上传前
    beforeUpload(file) {
      // console.log('beforeUpload file:',file)
      // 格式判断
      // 显示loading动画
      this.quillUpdateImg = true;
    },
    uploadSuccess(res, file) {
      let oss_domain =  'http://xinwei-oss-aliyun.oss-cn-beijing.aliyuncs.com/'
      let img_name = res.data.replace(oss_domain, ''); 
      // res为图片服务器返回的数据
      // 获取富文本组件实例
      let quill = this.$refs.myQuillEditor.quill;
      // 如果上传成功
      if (res.errcode == 0) {
        // 获取光标所在位置
        let length = quill.getSelection().index;
        // 插入图片  res.url为服务器返回的图片地址
        quill.insertEmbed(length, "image", res.data);
        var img = new Image();
        img.src = res.data;
        // console.log('图片img:'+JSON.stringify(img));
        // 加载完成执行后才能获取宽高
        img.onload = function(){
         // 获取图片的宽高
          console.log('width:'+img.width+',height:'+img.height);

          //更新内容数据
          let delta = quill.getContents();
          delta.ops = delta.ops.map(function(d){
            if(d.insert.image && d.insert.image==res.data){
              d.attributes = {height: img.height, width: img.width,name:img_name};
            }
            return d
          })
          quill.setContents(delta)
          quill.setSelection(length + 1);// 调整光标到最后
        };
      } else {
        this.$message.error("图片插入失败");
      }
      // loading动画消失
      this.quillUpdateImg = false;
    },
    // 富文本图片上传失败
    uploadError() {
      // loading动画消失
      this.quillUpdateImg = false;
      this.$message.error("图片插入失败");
    }, 
    saveHtml:function(){
      let that = this;

      that.param = that.listData
      if(that.listData.id>0){
        that.param.act = "update";
      }else{
        //新增
        that.param.act = "add";
      }

      //处理内容数据 start
      let quill = this.$refs.myQuillEditor.quill;
      let delta = quill.getContents();
      delta = delta.ops
      // console.log('编辑器内容:',delta);
      let temp = []
      for (var i = 0; i < delta.length; i++) {
        if( delta[i]['insert']['image'] ){
          temp.push({
            type: "img",
            src: delta[i]['insert']['image'],
            width: delta[i]['attributes']['width'],
            height: delta[i]['attributes']['height']
          })
        }
        else if(delta[i]['insert']['video']){
          temp.push({
            type: "video",
            src: delta[i]['insert']['video']
          })
        }
        else if(delta[i]['insert']['audio']){
          temp.push({
            type: "audio",
            src: delta[i]['insert']['audio']
          })
        }
        else if( typeof(delta[i]['insert'])=='string'){
          let text =  delta[i]['insert']
          let arr_f = text.replace(/\n/g,"<br>")
          let p_arr = arr_f.split("<br>");
          var j = 0
          for (j = 0; j<p_arr.length-1; j++) {
            temp.push({
              type: "text",
              text: p_arr[j]
            })
          }
        }else{
          console.log('err:delta[i]:',delta[i])
        }
      }
      //处理内容数据 end
      // console.log('temp:',temp);

      that.param.content = JSON.stringify(temp);
      // console.log("提交的param",that.param)
      // return false;
      NewsListEdit(that.param)
      .then(res => {
        // console.log("提交的res.data",res.errcode)
        if (res.errcode === 0) {
          that.$message({
            type: 'success',
            message: that.param.act=='update' ? '编辑成功' : '保存成功'
          })
          this.$router.push({ path: '/News/News' })
        } else {
          that.$message({
            type: 'info',
            message: res.errmsg
          })
        }
      })
      .catch(err => {
        that.loading = false
        that.$message.error('操作失败，请稍后再试！')
      })
    },
    //编辑框html过滤成文本
    filter(html){
      const pReg = /(<p>|<\/p>)/g,
          imgReg = /<img.*?(?:>|\/>)/gi,
          srcReg = /src=[\'\"]?([^\'\"]*)[\'\"]?/i,
          widthReg = /width=[\'\"]?([^\'\"]*)[\'\"]?/i,
          heightReg = /width=[\'\"]?([^\'\"]*)[\'\"]?/i;
      const imgMatch = html.replace(pReg, '').match(imgReg);
      // console.log("imgMatchq前",imgMatch)

      if (imgMatch != '' && imgMatch != null) {
        // console.log("imgMatch",imgMatch)
        imgMatch.map(src => ({
          type: "img",
          src: src.match(srcReg) && src.match(srcReg)[1] || 0,
          width: src.match(widthReg) && src.match(srcReg)[1] || 0,
          height: src.match(heightReg) && src.match(srcReg)[1] || 0,
        }))
      }

      //文本
      const textMatch = html.replace(pReg, "<text>").replace(imgReg, "<img>").split('<text>').map(text => {
            return {
            type: "text",
            text: text,
          }
        }).filter(v => v.text)

      //图片
      let imgIndex = 0
      const newMatch = textMatch.reduce((prev, curr) => {
        if(curr.text.includes('img') && imgIndex < imgMatch.length) {
          if(curr.text.indexOf("<img>")) {
            prev.push({
            ...curr,
            text: curr.text.replace("<img>", "")
            }, 
            imgMatch[imgIndex])
          } else {
            prev.push(imgMatch[imgIndex],{
            ...curr,
            text: curr.text.replace("<img>", "")
            })
          }
          imgIndex ++ 
        } else {
          prev.push(curr)
        }
        return prev
      }, [])

      return newMatch;
    },
    handleRemove(file, fileList) {
      console.log(file, fileList);
    },
    submitUpload() {
      this.$refs.upload.submit();
    },
    //获取图片链接 给参数赋值
    handleImgsSuccess(response,file,fileList){
      // console.log('response:',response)
      // console.log('file:',file)
      // console.log('fileList:',fileList)
      this.listData.imgs = response.data;
    },
    submitTopUpload() {
      this.$refs.topupload.submit();
    },
    //获取图片链接 给参数赋值
    handleTopSuccess(response,file,fileList){
      this.listData.topimgs = response.data
    }
  }
}
</script>

<style scoped>
  .ql-snow .ql-editor img{
    /*width:390px!important;
    height:241px!important;*/
    max-width: 100%;
    height: auto;
  }
  .avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  .avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }
  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
  }
  .avatar {
    width: 178px;
    height: 178px;
    display: block;
  }
  .el-upload-list--picture .el-upload-list__item {
    overflow: hidden;
    background-color: #fff;
    border: 1px solid #c0ccda;
    border-radius: 6px;
    box-sizing: border-box;
    margin-top: 10px;
    padding: 10px 10px 10px 90px;
    height: 92px;
    
  }
  .el-upload-list el-upload-list--picture{
    width: 250px!important;
  }
</style>

 



