<template>
  <div >
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>启动页管理</el-breadcrumb-item>
    </el-breadcrumb>
    <el-form class="user-search">
      <el-form-item>
        <el-button size="small" type="primary" icon="el-icon-plus" @click="handleAdd()">添加启动页</el-button>
      </el-form-item>
    </el-form>
    <div v-loading="loading" border element-loading-text="拼命加载中" style="margin-top:30px">
      <el-table size="small" :data="imglist" highlight-current-row v-loading="loading" border element-loading-text="拼命加载中" style="width: 80%;">
        <el-table-column prop="id" label="ID" width="50"></el-table-column>
        <el-table-column prop="img" label="Banner" width="200">
          <div  slot-scope="scope">
            <img class="table_img" :src="scope.row.img" alt=""/>
          </div>
        </el-table-column>
        <el-table-column prop="url" label="关联文章ID"></el-table-column>
        <el-table-column prop="statusname" label="状态" width="80"></el-table-column>
        <el-table-column sortable prop="sort" label="排序值" width="70">
        </el-table-column>
        <el-table-column align="center" label="操作" width="200">
          <template slot-scope="scope">
            <el-button size="mini" @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
              <el-button size="mini" type="danger" @click="delRecord(scope.$index, scope.row)" v-if="scope.row.status==1">禁用</el-button>
              <el-button size="mini" type="primary" @click="delRecord(scope.$index, scope.row)" v-else>显示</el-button>
          </template>
        </el-table-column>
      </el-table>
      <!-- 编辑界面 -->
      <el-dialog :title="title" :visible.sync="editFormVisible" width="40%" @click="closeDialog">
        <el-form label-width="120px" :model="editForm" :rules="rules" ref="editForm">
          <el-form-item label="ID" prop="id" >
            <el-input size="small" v-model="editForm.id" auto-complete="off" :disabled="true"></el-input>
          </el-form-item>
          <el-form-item label="icon" prop="img">
            <el-input size="small" v-model="editForm.img" auto-complete="off" placeholder="请输入icon地址"></el-input>
            <el-upload
              class="upload-demo"
              ref="upload2"
              action="/index.php/adm/index/upLoadImg"
              name="img"
              :limit="1"
              :on-success="handleAvatarSuccess2"
              :file-list="fileList"
              :auto-upload="false">
              <el-button slot="trigger" size="small" type="primary">选取文件</el-button>
              <el-button style="margin-left: 10px;" size="small" type="success" @click="submitUpload2">上传到服务器</el-button>
              <div slot="tip" class="el-upload__tip">只能上传jpg/png文件，且不超过500kb</div>
            </el-upload>
          </el-form-item>
          <el-form-item label="链接" prop="url">
            <el-input size="small" v-model="editForm.url" auto-complete="off" placeholder="请输入链接1"></el-input>
          </el-form-item>
          <el-form-item label="排序值" prop="sort">
            <el-input size="small" v-model="editForm.sort" auto-complete="off" placeholder="请输入排序值"></el-input>
          </el-form-item>
          <el-form-item label="状态" prop="status">
            <el-select size="small" v-model="editForm.status" placeholder="请选择" class="userRole">
              <el-option
              v-for="item in listStatus"
              :key="item.id"
              :label="item.name"
              :value="item.id">
            </el-option>
            </el-select>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button size="small" @click="closeDialog">取消</el-button>
          <el-button size="small" type="primary" :loading="loading" class="title" @click="submitEditForm()">保存</el-button>
        </div>
      </el-dialog>
      <!-- 新增界面 -->
      <el-dialog :title="title" :visible.sync="addFormVisible" width="40%" @click="closeDialog">
        <el-form label-width="120px" :model="addForm" :rules="rules" ref="editForm">
          <el-form-item label="icon" prop="img">
            <el-input size="small" v-model="addForm.img" auto-complete="off" placeholder="请输入icon地址"></el-input>
            <el-upload
              class="upload-demo"
              ref="upload"
              action="/index.php/adm/index/upLoadImg"
              name="img"
              :limit="1"
              :on-success="handleAvatarSuccess"
              :file-list="fileList"
              :auto-upload="false">
              <el-button slot="trigger" size="small" type="primary">选取文件</el-button>
              <el-button style="margin-left: 10px;" size="small" type="success" @click="submitUpload">上传到服务器</el-button>
              <div slot="tip" class="el-upload__tip">只能上传jpg/png文件，且不超过500kb</div>
            </el-upload>
          </el-form-item>
          <el-form-item label="链接" prop="url">
            <el-input size="small" v-model="addForm.url" auto-complete="off" placeholder="请输入链接"></el-input>
          </el-form-item>
          <el-form-item label="排序值" prop="sort">
            <el-input size="small" v-model="addForm.sort" auto-complete="off" placeholder="请输入排序值"></el-input>
          </el-form-item>
          <el-form-item label="状态" prop="status">
            <el-select size="small" v-model="addForm.status" placeholder="请选择" class="userRole">
              <el-option
              v-for="item in listStatus"
              :key="item.id"
              :label="item.name"
              :value="item.id">
            </el-option>
            </el-select>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button size="small" @click="closeDialog">取消</el-button>
          <el-button size="small" type="primary" :loading="loading" class="title" @click="submitAddForm()">新增</el-button>
        </div>
      </el-dialog>
    </div>
  </div>
</template>
<script>
import {
  getNewsImgList,
  imgDo
} from '../../api/apiConfig'
  export default {
    data() {
      return {
        loading: false, //是显示加载
        title: '添加启动页',
        imglist: [],
        editForm: {
          act:"update",
          id: '',
          category:"1",
          img: '',
          status: '',
          url: '',
          sort: '',
          token: localStorage.getItem('logintoken')
        },
        addForm: {
          act:"add",
          category:"1",
          img: '',
          status: '',
          url: '',
          sort: '',
          token: localStorage.getItem('logintoken')
        },
        listStatus:[{id:1,name:"正常"},{id:2,name:"禁用"}],
        editFormVisible: false,
        addFormVisible: false,
        rules: {
          sort: [
            { required: true, message: '请输入排序值', trigger: 'blur' }
          ],
          status: [{ required: true, message: '请选择状态', trigger: 'blur' }]
        },
        param1: {
          category:1
        },
        fileList: [],
      };
    },
    /**
     * 创建完毕
     */
    created() {
      this.getdata(this.param1)
    },
    methods: {
      handleRemove(file, fileList) {
        console.log(file, fileList);
      },
      // 获取数据
      getdata(param1) {
        let status_list = {1:'正常',2:'禁用'}
        getNewsImgList(param1)
        .then(res => {
          this.loading = false;
          //登录过期
          if(res.errcode == 10006){
            this.$store.commit('logout', 'false')
            this.$router.push({ path: '/login' })
            return false
          }

          if (res.errcode === 0) {
            let imglist = res.data.imglist;
            for (var i = imglist.length - 1; i >= 0; i--) {
              let statusid = imglist[i]['status']
              imglist[i]['statusname'] = status_list[statusid] ? status_list[statusid] : statusid
            }
            this.imglist  = imglist
          }
          else {
            this.$message({
              type: 'info',
              message: res.errmsg
            })
          }
        })
        .catch(err => {
          this.loading = false
          this.$message.error('数据加载失败，请稍后再试！')
        })
      },
      //删除
      delRecord(index, row) {
        let param = {
          act:"upstatus",
          status:2,
          id:row.id
        };
        param.status = row.status==1 ? 2 : 1
        let actname = row.status==1 ? '禁用' : '显示'
        this.$confirm('确定要'+actname+'吗?', '信息', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        })
          .then(() => {
            imgDo(param)
              .then(res => {
                if (res.errcode === 0) {
                  this.$message({
                    type: 'success',
                    message: '操作成功!'
                  })
                  this.getdata(this.param1)
                } else {
                  this.$message({
                    type: 'info',
                    message: res.errmsg
                  })
                }
              })
              .catch(err => {
                this.loading = false
                this.$message.error('删除失败，请稍后再试！')
              })
          })
          .catch((err) => {
            console.log('err:',err)
            // this.$message({
            //   type: 'info',
            //   message: '已取消删除'
            // })
          })
      },
      //显示新界面
      handleAdd: function(index, row) {
        this.addFormVisible = true
        this.title = '添加启动页'
        this.type = 2
        this.addForm.img = ''
        this.addForm.url = ''
        this.addForm.sort = 1
        this.addForm.status = 1
      },
      submitAddForm() {
        imgDo(this.addForm)
          .then(res => {
            this.addFormVisible = false
            this.loading = false
            if (res.errcode === 0) {
              this.getdata(this.param1)
              this.$message({
                type: 'success',
                message: '新增成功！'
              })
            } else {
              this.$message({
                type: 'info',
                message: res.errmsg
              })
            }
          })
          .catch(err => {
            this.addFormVisible = false
            this.loading = false
            this.$message.error('保存失败，请稍后再试！')
          })
      },
      //显示编辑界面
      handleEdit: function(index, row) {
        this.editFormVisible = true
        this.title = '修改启动页'
        this.type = 1
        this.editForm.id = row.id
        this.editForm.sort = row.sort
        this.editForm.img = row.img
        this.editForm.url = row.url
        this.editForm.status = row.status
      },
      // 编辑页面保存方法
      submitEditForm() {
        // console.log("editData:",this.editForm)
        imgDo(this.editForm)
          .then(res => {
            this.editFormVisible = false
            this.loading = false
            if (res.errcode === 0) {
              this.getdata(this.param1)
              this.$message({
                type: 'success',
                message: '保存成功！'
              })
            } else {
              this.$message({
                type: 'info',
                message: res.errmsg
              })
            }
          })
          .catch(err => {
            this.editFormVisible = false
            this.loading = false
            this.$message.error('保存失败，请稍后再试！')
          })
      },
      // 关闭编辑、增加弹出框
      closeDialog() {
        this.editFormVisible = false
        this.addFormVisible = false
      },
      beforeremove(file,fileList){
        let that = this;
          that.$confirm('此操作将删除当前会场, 是否继续?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                that.asyncReq(file,fileList) // 在这里真正的处理图片列表
            }).catch(() => {
                that.$message({
                    type: 'info',
                    message: '已取消删除'
                });
          });
          return false; // 这是重点,不管上面的操作结果如何都返回false
          },
        asyncReq (file,fileList) {
          // 处理图片列表
          // 远请求服务器如果成功则把fileList中要删除的file移除即可
          let that = this;
          console.log("asyncReqfileList:",fileList);
          console.log("asyncReqfile:",file);
          let data = {    
            act:"delete",
            id:file.id
          }

          imgDo({})
          .then(function(res) {
            console.log("res",res);
            //登录过期
            if(res.data.errcode == 10006){
              this.$store.commit('logout', 'false')
              this.$router.push({ path: '/login' })
              return false
            }
            
            if (res.data.errcode === 0) {
              console.log("删除成功")
              that.getdata(that.param1);
              that.$message({
                type: 'success',
                message: res.data.errmsg
              })
            } else {
              console.log("删除失败")
              that.$message({
                type: 'info',
                message: res.data.errmsg
              })
            }
          })
      },
      submitUpload() {
        this.$refs.upload.submit();
      },
      //获取图片链接 给参数赋值
      handleAvatarSuccess(response,file,fileList){
        this.addForm.img = response.data;
      },
      submitUpload2() {
        this.$refs.upload2.submit();
      },
      //获取图片链接 给参数赋值
      handleAvatarSuccess2(response,file,fileList){
        this.editForm.img = response.data;
      }
    }
  }
</script>
<style>
  .user-search {
    margin-top: 20px;
  }
  .table_img{
    max-width: 100%;
    height: auto;
  }
  .form{
    margin-left: 70px;
    margin-top: 30px;
  }
  .form{
    margin-left: 70px;
    margin-top: 30px;
  }
  .upload_case{
    width:100px;
    height:200px;
  }
  .el-upload-list--picture-card .el-upload-list__item{
      height:250px;
  }
  .el-upload--picture-card{
    height:250px;
  }
  .el-upload--picture-card i{
    margin-top: 110px;
  }
</style>



