/**
 * 管理员管理 
 */
<template>
  <div>
    <!-- 面包屑导航 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>用户管理</el-breadcrumb-item>
    </el-breadcrumb>
    <!-- <el-form class="user-search">
      <el-form-item>
        <el-button size="small" type="primary" icon="el-icon-plus" @click="handleEdit()">添加</el-button>
      </el-form-item>
    </el-form> -->
    <!-- 搜索筛选 -->
    <el-form :inline="true" :model="formInline" class="user-search">
      <el-form-item label="搜索：">
        <el-input size="small" v-model="formInline.keywords" placeholder="输入账号或昵称"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button size="small" type="primary" icon="el-icon-search" @click="search">搜索</el-button>
      </el-form-item>
    </el-form>
    <!--列表-->
    <el-table size="small" :data="listData" highlight-current-row v-loading="loading" border element-loading-text="拼命加载中" style="width: 100%;">
      <el-table-column prop="id" label="用户ID" width="80">
      </el-table-column>
      <el-table-column prop="name" label="昵称" width="100">
      </el-table-column>
      <el-table-column  prop="phone" label="账号">
      </el-table-column>
      <el-table-column prop="headimg" label="头像" width="80">
        <div  slot-scope="scope">
          <img class="table_img" :src="scope.row.headimg" alt=""/>
        </div>
      </el-table-column>
      <el-table-column sortable prop="sex" label="性别" width="80">
      </el-table-column>
      <el-table-column prop="country" label="国家" width="100">
      </el-table-column>
      <el-table-column sortable prop="province" label="省份" width="100">
      </el-table-column>
      <el-table-column sortable prop="city" label="城市" width="100">
      </el-table-column>
      <el-table-column sortable prop="age" label="年龄" width="80">
      </el-table-column>
      <!-- <el-table-column sortable prop="weibo" label="博客" width="100"></el-table-column> -->
      <!-- <el-table-column sortable prop="role" label="角色" width="80"></el-table-column> -->
      <el-table-column sortable prop="time_create" label="新增时间">
        <template slot-scope="scope">
          <div>{{scope.row.time_create|timestampToTime}}</div>
        </template>
      </el-table-column>
      <el-table-column align="center" label="操作" min-width="">
        <template slot-scope="scope">
          <el-button type="danger" size="mini" @click="setAdmin(scope.$index, scope.row)">设为管理员</el-button>
        </template>
      </el-table-column>
    </el-table>
    <!-- 分页组件 -->
    <Pagination v-bind:child-msg="pageparm" @callFather="callFather" ></Pagination>
  </div>
</template>

<script>
import {
  userConfigList,
  adminSet
} from '../../api/apiConfig'
import Pagination from '../../components/Pagination'
export default {
  data() {
    return {
      nshow: true, //switch开启
      fshow: false, //switch关闭
      loading: false, //是显示加载
      formInline: {
        keywords:"",
        token: localStorage.getItem('logintoken'),
      },
      listData: [], //用户数据
      // 分页参数
      pageparm: {
        currentPage: 1,
        pageSize: 10,
        total: 10
      }
    }
  },
  // 注册组件
  components: {
    Pagination
  },
  /**
   * 数据发生改变
   */

  /**
   * 创建完毕
   */
  created() {
    this.getdata(this.formInline)
  },

  /**
   * 里面的方法只有被调用才会执行
   */
  methods: {
    // 获取管理员列表
    getdata(parameter) {
      this.loading = true
      /***
       * 调用接口
       */
      userConfigList(parameter)
        .then(res => {
          this.loading = false
          //登录过期
          if(res.errcode == 10006){
            this.$store.commit('logout', 'false')
            this.$router.push({ path: '/login' })
            return false
          }
          if (res.errcode === 0) {
            this.listData = res.data.memberlist;
            // 分页赋值
            this.pageparm.currentPage = res.data.current_page
            this.pageparm.total = res.data.total_count
          } else {
            this.$message({
              type: 'info',
              message: res.errmsg
            })
          }
        })
        .catch(err => {
          this.loading = false
          console.log('err:',err)
          this.$message.error('数据加载失败，请稍后再试！')
        })
    },
    // 分页插件事件
    callFather(parm) {
      this.formInline.page = parm.currentPage
      this.formInline.pagesize = parm.pageSize
      this.getdata(this.formInline)
    },
    // 搜索事件
    search() {
      this.getdata(this.formInline)
    },
    setAdmin(index, row) {
      this.$confirm('确定要删除吗?', '信息', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
      .then(() => {
        adminSet({id:row.id})
          .then(res => {
            if (res.errcode===0) {
              this.$message({
                type: 'success',
                message: '设置成功，请到管理员列表查询!'
              })
              this.getdata(this.formInline)
            } else {
              this.$message({
                type: 'info',
                message: res.errmsg
              })
            }
          })
          .catch(err => {
            this.loading = false
            this.$message.error('设置失败，请稍后再试！')
          })
      })
      .catch(() => {
        this.$message({
          type: 'info',
          message: '已取消'
        })
      })
    },
    // 关闭编辑、增加弹出框
    closeDialog() {
      this.editFormVisible = false
    }
  }
}
</script>

<style scoped>
.user-search {
  margin-top: 20px;
}
.userRole {
  width: 100%;
}
.table_img{
  width: 35px;
  height: 35px;
}
</style>

 



