<template>
  <div>
    <!-- 面包屑导航 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>首页栏目列表</el-breadcrumb-item>
    </el-breadcrumb>
    <el-form class="user-search">
      <el-form-item>
        <el-button size="small" type="primary" icon="el-icon-plus" @click="handleAdd()">添加分类</el-button>
      </el-form-item>
    </el-form>
    <div v-loading="loading" border element-loading-text="拼命加载中" style="margin: 20px;">
      <el-table size="small" :data="imglist" highlight-current-row v-loading="loading" border element-loading-text="拼命加载中" style="width: 100%;">
        <el-table-column prop="id" label="ID" width="50"></el-table-column>
        <el-table-column prop="title" label="名称" width="160"></el-table-column>
        <el-table-column prop="img" label="图标" width="120">
          <div  slot-scope="scope">
            <img class="table_img" :src="scope.row.img" alt=""/>
          </div>
        </el-table-column>
        <el-table-column prop="url" label="链接"></el-table-column>
        <el-table-column prop="statusname" label="状态" width="80"></el-table-column>
        <el-table-column sortable prop="sort" label="排序值" width="70">
        </el-table-column>
        <el-table-column align="center" label="操作" width="200">
          <template slot-scope="scope">
            <el-button size="mini" @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
              <el-button size="mini" type="danger" @click="delRecord(scope.$index, scope.row)" v-if="scope.row.status==1">禁用</el-button>
              <el-button size="mini" type="primary" @click="delRecord(scope.$index, scope.row)" v-else>显示</el-button>
          </template>
        </el-table-column>
      </el-table>
      <!-- 编辑界面 -->
      <el-dialog :title="title" :visible.sync="editFormVisible" width="40%" @click="closeDialog">
        <el-form label-width="120px" :model="editForm" :rules="rules" ref="editForm">
          <el-form-item label="ID" prop="id" >
            <el-input size="small" v-model="editForm.id" auto-complete="off" :disabled="true"></el-input>
          </el-form-item>
          <el-form-item label="名称" prop="title">
            <el-input size="small" v-model="editForm.title" auto-complete="off" placeholder="请输入名称"></el-input>
          </el-form-item>
          <el-form-item label="icon" prop="img">
            <el-input size="small" v-model="editForm.img" auto-complete="off" placeholder="请输入icon地址"></el-input>
            <el-upload
              class="upload-demo"
              ref="upload2"
              action="/index.php/adm/index/upLoadImg"
              name="img"
              :limit="1"
              :on-success="handleAvatarSuccess2"
              :file-list="fileList2"
              :auto-upload="false">
              <el-button slot="trigger" size="small" type="primary">选取文件</el-button>
              <el-button style="margin-left: 10px;" size="small" type="success" @click="submitUpload2">上传到服务器</el-button>
              <div slot="tip" class="el-upload__tip">只能上传jpg/png文件，且不超过500kb</div>
            </el-upload>
          </el-form-item>
          <el-form-item label="链接" prop="url">
            <el-input size="small" v-model="editForm.url" auto-complete="off" placeholder="请输入链接"></el-input>
          </el-form-item>
          <el-form-item label="排序值" prop="sort">
            <el-input size="small" v-model="editForm.sort" auto-complete="off" placeholder="请输入排序值"></el-input>
          </el-form-item>
          <el-form-item label="状态" prop="status">
            <el-select size="small" v-model="editForm.status" placeholder="请选择" class="userRole">
              <el-option
              v-for="item in listStatus"
              :key="item.id"
              :label="item.name"
              :value="item.id">
            </el-option>
            </el-select>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button size="small" @click="closeDialog">取消</el-button>
          <el-button size="small" type="primary" :loading="loading" class="title" @click="submitEditForm()">保存</el-button>
        </div>
      </el-dialog>
      <!-- 新增界面 -->
      <el-dialog :title="title" :visible.sync="addFormVisible" width="40%" @click="closeDialog">
        <el-form label-width="120px" :model="addForm" :rules="rules" ref="addForm">
          <el-form-item label="名称" prop="title">
            <el-input size="small" v-model="addForm.title" auto-complete="off" placeholder="请输入名称"></el-input>
          </el-form-item>
          <el-form-item label="icon" prop="img">
            <el-input size="small" v-model="addForm.img" auto-complete="off" placeholder="请输入icon地址"></el-input>
            <el-upload
              class="upload-demo"
              ref="upload"
              action="/index.php/adm/index/upLoadImg"
              name="img"
              :limit="1"
              :on-success="handleAvatarSuccess"
              :file-list="fileList"
              :auto-upload="false">
              <el-button slot="trigger" size="small" type="primary">选取文件</el-button>
              <el-button style="margin-left: 10px;" size="small" type="success" @click="submitUpload">上传到服务器</el-button>
              <div slot="tip" class="el-upload__tip">只能上传jpg/png文件，且不超过500kb</div>
            </el-upload>
          </el-form-item>
          <el-form-item label="链接" prop="url">
            <el-input size="small" v-model="addForm.url" auto-complete="off" placeholder="请输入链接"></el-input>
          </el-form-item>
          <el-form-item label="排序值" prop="sort">
            <el-input size="small" v-model="addForm.sort" auto-complete="off" placeholder="请输入排序值"></el-input>
          </el-form-item>
          <el-form-item label="状态" prop="status">
            <el-select size="small" v-model="addForm.status" placeholder="请选择" class="userRole">
              <el-option
              v-for="item in listStatus"
              :key="item.id"
              :label="item.name"
              :value="item.id">
            </el-option>
            </el-select>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button size="small" @click="closeDialog">取消</el-button>
          <el-button size="small" type="primary" :loading="loading" class="title" @click="submitAddForm()">新增</el-button>
        </div>
      </el-dialog>
    </div>
  </div>
</template>
<script>
import {
  getNewsImgList,
  imgDo
} from '../../api/apiConfig'
  export default {
    data() {
      return {
        form: {//form里面的参数
            name: ''
         },
        param:  {
          act:"add",
          category:"3",
          title:"标题可不填",
          img:"./static/logo.png",
          url:"#",
          sort:"1",
        },//表单要提交的参数
        title: '添加栏目',
        editForm: {
          act:"update",
          id: '',
          category:"3",
          title: '',
          img: '',
          status: '',
          url: '',
          sort: '',
          token: localStorage.getItem('logintoken')
        },
        addForm: {
          act:"add",
          category:"3",
          title: '',
          img: '',
          status: '',
          url: '',
          sort: '',
          token: localStorage.getItem('logintoken')
        },
        listStatus:[{id:1,name:"正常"},{id:2,name:"禁用"}],
        dialogImageUrl: '',
        editFormVisible: false,
        addFormVisible: false,
        loading: false, //是显示加载
        imglist: [],
        rules: {
          title: [
            { required: true, message: '请输入名称', trigger: 'blur' }
          ],
          sort: [
            { required: true, message: '请输入排序值', trigger: 'blur' }
          ],
          status: [{ required: true, message: '请选择状态', trigger: 'blur' }]
        },
        param1: {
          category:3
        },
        fileList: [],
        fileList2: [],
      };
    },
    /**
     * 创建完毕
     */
    created() {
      this.getdata(this.param1)
    },
    methods: {
      // 获取引导图
      getdata(param1) {
        this.loading = true;
        let status_list = {1:'正常',2:'禁用'}
        getNewsImgList(param1)
        .then(res => {
          this.loading = false;
          if (res.errcode === 0) {
            let imglist = res.data.imglist;
            for (var i = imglist.length - 1; i >= 0; i--) {
              let statusid = imglist[i]['status']
              imglist[i]['statusname'] = status_list[statusid] ? status_list[statusid] : statusid
            }
            this.imglist  = imglist
          } else {
            this.$message({
              type: 'info',
              message: res.errmsg
            })
          }
        })
        .catch(err => {
          this.loading = false
          this.$message.error('菜单加载失败，请稍后再试！')
        })
      },
      // 删除
      delRecord(index, row) {
        let param = {
          act:"upstatus",
          status:2,
          id:row.id
        };
         param.status = row.status==1 ? 2 : 1
        let actname = row.status==1 ? '禁用' : '显示'
        this.$confirm('确定要'+actname+'吗?', '信息', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        })
          .then(() => {
            imgDo(param)
              .then(res => {
                if (res.errcode === 0) {
                  this.$message({
                    type: 'success',
                    message: '资讯类别已删除!'
                  })
                  this.getdata(this.param1)
                } else {
                  this.$message({
                    type: 'info',
                    message: res.errmsg
                  })
                }
              })
              .catch(err => {
                this.loading = false
                this.$message.error('删除失败，请稍后再试！')
              })
          })
          .catch((err) => {
            console.log('err:',err)
            // this.$message({
            //   type: 'info',
            //   message: '已取消删除'
            // })
          })
      },
       //显示新界面
      handleAdd: function(index, row) {
        this.addFormVisible = true
        this.title = '添加栏目'
        this.type = 2
        this.addForm.title = ''
        this.addForm.img = ''
        this.addForm.url = ''
        this.addForm.sort = 1
        this.addForm.status = 1
      },
      submitAddForm() {
        imgDo(this.addForm)
          .then(res => {
            this.addFormVisible = false
            this.loading = false
            if (res.errcode === 0) {
              this.getdata(this.param1)
              this.$message({
                type: 'success',
                message: '新增成功！'
              })
            } else {
              this.$message({
                type: 'info',
                message: res.errmsg
              })
            }
          })
          .catch(err => {
            this.addFormVisible = false
            this.loading = false
            this.$message.error('保存失败，请稍后再试！')
          })
      },
      //显示编辑界面
      handleEdit: function(index, row) {
        this.editFormVisible = true
        this.title = '修改列别'
        this.type = 1
        this.editForm.id = row.id
        this.editForm.title = row.title
        this.editForm.sort = row.sort
        this.editForm.img = row.img
        this.editForm.url = row.url
        this.editForm.status = row.status
      },
      // 编辑页面保存方法
      submitEditForm() {
        // console.log("editData:",this.editForm)
        imgDo(this.editForm)
          .then(res => {
            this.editFormVisible = false
            this.loading = false
            if (res.errcode === 0) {
              this.getdata(this.param1)
              this.$message({
                type: 'success',
                message: '保存成功！'
              })
            } else {
              this.$message({
                type: 'info',
                message: res.errmsg
              })
            }
          })
          .catch(err => {
            this.editFormVisible = false
            this.loading = false
            this.$message.error('保存失败，请稍后再试！')
          })
      },
      // 关闭编辑、增加弹出框
      closeDialog() {
        this.editFormVisible = false
        this.addFormVisible = false
      },
      submitUpload() {
        this.$refs.upload.submit();
      },
      //获取图片链接 给参数赋值
      handleAvatarSuccess(response,file,fileList){
        this.addForm.img = response.data;
      },
      submitUpload2() {
        this.$refs.upload2.submit();
      },
      //获取图片链接 给参数赋值
      handleAvatarSuccess2(response,file,fileList){
        this.editForm.img = response.data;
      }
    }
  }
</script>
<style>
 img{
  max-width: 100%;
  height: auto;
 }
  .user-search {
    margin-top: 20px;
  }
  .form{
    margin-left: 70px;
    margin-top: 30px;
  }
</style>



