/**
 * 热搜榜管理 
 */
 <template>
  <!-- 面包屑导航 -->
  <div >
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>热搜榜管理</el-breadcrumb-item>
    </el-breadcrumb>
    <el-form class="user-search">
      <el-form-item>
        <el-button size="small" type="primary" icon="el-icon-plus" @click="handleAdd()">添加热搜</el-button>
      </el-form-item>
    </el-form>
    <div v-loading="loading" border element-loading-text="拼命加载中" style="margin-top:30px">
      <el-table size="small" :data="datalist" highlight-current-row v-loading="loading" border element-loading-text="拼命加载中" style="width: 80%;">
        <el-table-column prop="id" label="ID" width="50"></el-table-column>
        <el-table-column prop="typename" label="类型" width="80"></el-table-column>
        <el-table-column prop="content" label="热搜"></el-table-column>
        <el-table-column prop="statusname" label="类型" width="80"></el-table-column>
        <el-table-column sortable prop="hot" label="热度" width="80"></el-table-column>
        <el-table-column sortable prop="sort" label="排序值" width="80"></el-table-column>
        <el-table-column align="center" label="操作" width="200">
          <template slot-scope="scope">
            <el-button size="mini" @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
            <el-button size="mini" type="danger" @click="delRecord(scope.$index, scope.row)" v-if="scope.row.status==1">不显示</el-button>
            <el-button size="mini" type="primary" @click="delRecord(scope.$index, scope.row)" v-else>显示</el-button>
          </template>
        </el-table-column>
      </el-table>
      <!-- 编辑界面 -->
      <el-dialog :title="title" :visible.sync="editFormVisible" width="60%" @click="closeDialog">
        <el-form label-width="120px" :model="editForm" :rules="rules" ref="editForm">
          <el-form-item label="ID" prop="id" >
            <el-input size="small" v-model="editForm.id" auto-complete="off" :disabled="true"></el-input>
          </el-form-item>
          <el-form-item label="类型" prop="type">
            <el-select size="small" v-model="editForm.type" placeholder="请选择类型" class="userRole">
              <el-option
              v-for="item in listType"
              :key="item.id"
              :label="item.name"
              :value="item.id">
            </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="热搜词" prop="content">
            <el-input size="small" v-model="editForm.content" auto-complete="off" placeholder="热搜词"></el-input>
          </el-form-item>
          <el-form-item label="热度" prop="content">
            <el-input size="small" v-model="editForm.hot" auto-complete="off" placeholder="热度"></el-input>
          </el-form-item>
          <el-form-item label="排序值" prop="sort">
            <el-input size="small" v-model="editForm.sort" auto-complete="off" placeholder="请输入排序值"></el-input>
          </el-form-item>
          <el-form-item label="状态" prop="status">
            <el-select size="small" v-model="editForm.status" placeholder="请选择" class="userRole">
              <el-option
              v-for="item in listStatus"
              :key="item.id"
              :label="item.name"
              :value="item.id">
            </el-option>
            </el-select>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button size="small" @click="closeDialog">取消</el-button>
          <el-button size="small" type="primary" :loading="loading" class="title" @click="submitEditForm()">保存</el-button>
        </div>
      </el-dialog>
      <!-- 新增界面 -->
      <el-dialog :title="title" :visible.sync="addFormVisible" width="60%" @click="closeDialog">
        <el-form label-width="120px" :model="addForm" :rules="rules" ref="editForm">
          <el-form-item label="类型" prop="type">
            <el-select size="small" v-model="addForm.type" placeholder="请选择类型" class="userRole">
              <el-option
              v-for="item in listType"
              :key="item.id"
              :label="item.name"
              :value="item.id">
            </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="热搜词" prop="content">
            <el-input size="small" v-model="addForm.content" auto-complete="off" placeholder="热搜词"></el-input>
          </el-form-item>
          <el-form-item label="热度" prop="content">
            <el-input size="small" v-model="addForm.hot" auto-complete="off" placeholder="热度"></el-input>
          </el-form-item>
          <el-form-item label="排序值" prop="sort">
            <el-input size="small" v-model="addForm.sort" auto-complete="off" placeholder="请输入排序值"></el-input>
          </el-form-item>
          <el-form-item label="状态" prop="status">
            <el-select size="small" v-model="addForm.status" placeholder="请选择" class="userRole">
              <el-option
              v-for="item in listStatus"
              :key="item.id"
              :label="item.name"
              :value="item.id">
            </el-option>
            </el-select>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button size="small" @click="closeDialog">取消</el-button>
          <el-button size="small" type="primary" :loading="loading" class="title" @click="submitAddForm()">新增</el-button>
        </div>
      </el-dialog>
    </div>
  </div>
</template>
<script>
import {
  getHotsearchList,
  hotsearchDo
} from '../../api/apiConfig'
  export default {
    data() {
      return {
        dialogImageUrl: '',
        dialogVisible: false,
        loading: false, //是显示加载
        title: '添加热搜',
        editForm: {
          act:"update",
          id: '',
          type: 1,
          status: '',
          content: '',
          hot: 0,
          sort: 255,
          token: localStorage.getItem('logintoken')
        },
        addForm: {
          act:"add",
          type: 1,
          status: 1,
          content: '',
          hot: 0,
          sort: 255,
          token: localStorage.getItem('logintoken')
        },
        listStatus:[{id:1,name:"正常"},{id:2,name:"不显示"}],
        listType:[{id:1,name:"文章"},{id:2,name:"帖子"}],
        editFormVisible: false,
        addFormVisible: false,
        rules: {
          sort: [
            { required: true, message: '请输入排序值', trigger: 'blur' }
          ],
          status: [{ required: true, message: '请选择状态', trigger: 'blur' }]
        },
        datalist: [],
        param1: {
          category:2
        },
      };
    },
    /**
     * 创建完毕
     */
    created() {
      this.getdata(this.param1)
    },
    methods: {
      // 获取数据
      getdata(param1) {
        this.loading = true;
        let status_list = {1:'正常',2:'不显示'}
        let type_list = {1:'文章',2:'帖子'}
        getHotsearchList(param1)
        .then(res => {
          this.loading = false;
          if (res.errcode === 0) {
            let datalist = res.data.datalist;
            for (var i = datalist.length - 1; i >= 0; i--) {
              let statusid = datalist[i]['status']
              datalist[i]['statusname'] = status_list[statusid] ? status_list[statusid] : statusid
               let typeid = datalist[i]['type']
              datalist[i]['statusname'] = type_list[typeid] ? type_list[typeid] : typeid
            }
            this.datalist  = datalist
          } else {
            this.$message({
              type: 'info',
              message: res.errmsg
            })
          }
        })
        .catch(err => {
          this.loading = false
          this.$message.error('数据加载失败，请稍后再试！')
          console.log('err:',err)
        })
      },
      //删除
      delRecord(index, row) {
        let param = {
          act:"upstatus",
          status:2,
          id:row.id
        };
        param.status = row.status==1 ? 2 : 1
        let actname = row.status==1 ? '禁用' : '显示'
        this.$confirm('确定要'+actname+'吗?', '信息', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        })
          .then(() => {
            hotsearchDo(param)
              .then(res => {
                if (res.errcode === 0) {
                  this.$message({
                    type: 'success',
                    message: '操作成功!'
                  })
                  this.getdata(this.param1)
                } else {
                  this.$message({
                    type: 'info',
                    message: res.errmsg
                  })
                }
              })
              .catch(err => {
                this.loading = false
                this.$message.error('操作失败，请稍后再试！')
              })
          })
          .catch((err) => {
            console.log('err:',err)
            // this.$message({
            //   type: 'info',
            //   message: '已取消删除'
            // })
          })
      },
      //显示新界面
      handleAdd: function(index, row) {
        this.addFormVisible = true
        this.title = '添加热搜'
      },
      submitAddForm() {
        hotsearchDo(this.addForm)
          .then(res => {
            this.addFormVisible = false
            this.loading = false
            if (res.errcode === 0) {
              this.getdata(this.param1)
              this.$message({
                type: 'success',
                message: '新增成功！'
              })
            } else {
              this.$message({
                type: 'info',
                message: res.errmsg
              })
            }
          })
          .catch(err => {
            this.addFormVisible = false
            this.loading = false
            this.$message.error('保存失败，请稍后再试！')
          })
      },
      //显示编辑界面
      handleEdit: function(index, row) {
        this.editFormVisible = true
        this.title = '修改热搜'
        this.editForm.id = row.id
        this.editForm.type = row.type
        this.editForm.content = row.content
        this.editForm.status = row.status
        this.editForm.hot = row.hot
        this.editForm.sort = row.sort
      },
      // 编辑页面保存方法
      submitEditForm() {
        // console.log("editData:",this.editForm)
        hotsearchDo(this.editForm)
          .then(res => {
            this.editFormVisible = false
            this.loading = false
            if (res.errcode === 0) {
              this.getdata(this.param1)
              this.$message({
                type: 'success',
                message: '保存成功！'
              })
            } else {
              this.$message({
                type: 'info',
                message: res.errmsg
              })
            }
          })
          .catch(err => {
            this.editFormVisible = false
            this.loading = false
            this.$message.error('保存失败，请稍后再试！')
          })
      },
      // 关闭编辑、增加弹出框
      closeDialog() {
        this.editFormVisible = false
        this.addFormVisible = false
      }
    }
  }
</script>
<style>
  .user-search {
    margin-top: 20px;
  }
  .table_img{
    max-width: 100%;
    height: auto;
  }
  .form{
    margin-left: 70px;
    margin-top: 30px;
  }
</style>



