/**
 * 管理员管理 
 */
<template>
  <div>
    <!-- 面包屑导航 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item :to="{ path: '/News/News' }">资讯列表</el-breadcrumb-item>
      <el-breadcrumb-item>资讯评论列表</el-breadcrumb-item>
    </el-breadcrumb>
    <div style="padding: 30px;">
      <!--列表-->
      <el-table size="small" :data="listData" highlight-current-row v-loading="loading" element-loading-text="拼命加载中" style="width: 100%;">
        <el-table-column prop="id" label="ID" width="80"></el-table-column>
        <el-table-column sortable prop="news_id" label="资讯ID" width="100"></el-table-column>
        <el-table-column sortable prop="member_name" label="昵称" width="200">
        </el-table-column>
        <el-table-column sortable prop="menber_heading" label="成员头像" width="150">
          <img class="table_img" src="../../assets/img/logo.png" alt=""/>
        </el-table-column>
        <el-table-column sortable prop="comment" label="评论时间" width="180">
          <template slot-scope="scope">
            <div>{{scope.row.time_create|timestampToTime}}</div>
          </template>
        </el-table-column>
        <el-table-column sortable prop="comment" label="评论内容" min-width="200"></el-table-column>
        <el-table-column align="center" label="操作" width="100">
          <template slot-scope="scope">
            <el-button size="mini" type="danger" @click="deleteCommont(scope.$index, scope.row)">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
          <!-- <Pagination v-bind:child-msg="pageparm" @callFather="callFather"></Pagination> -->
      </div>
    </div>
  </div>
</template>

<script>
import {
  NewscommentList,
  NewscommentDel
} from '../../api/apiConfig'
import Pagination from '../../components/Pagination'
export default {
  data() {
    return {
      nshow: true, //switch开启
      fshow: false, //switch关闭
      loading: false, //是显示加载
      editFormVisible: false, //控制编辑页面显示与隐藏
      title: '添加',
      param: {
        news_id:0
      },
      detailData: {
        title:'',
        views:'',
        loves:'',
        comment:'',
        author_name:'',
        author_headimg:''
      }, //社区内容详情
      head_img:'',
      listData: [], //社区内容评论列表
      // 分页参数
      pageparm: {
        currentPage: 1,
        pageSize: 10,
        total: 10
      }
    }
  },
  // 注册组件
  components: {
    Pagination
  },
  /**
   * 数据发生改变
   */

  /**
   * 创建完毕
   */
  created() {
  },
  mounted () {
    this.pageInit();
  },
  /**
   * 里面的方法只有被调用才会执行
   */
  methods: {
    pageInit(){
      let list = this.$route.query;
      this.param.news_id = list.id;
      this.getdata(this.param)
    },
    //评论列表
    getdata(param) {
      this.loading = true
      NewscommentList(param)
        .then(res => {
          this.loading = false
          if (res.errcode === 0) {
            this.listData = res.data.commentlist;
          } else {
            this.$message({
              type: 'info',
              message: res.errmsg
            })
          }
        })
        .catch(err => {
          this.loading = false
          this.$message.error('加载失败，请稍后再试！')
        })
    },
    // 分页插件事件
    callFather(parm) {
      this.formInline.page = parm.currentPage
      // this.formInline.limit = parm.pageSize
      this.getataList(this.formInline)
    },
    // 删除公司
    deleteCommont(index, row) {
      this.$confirm('确定要删除吗?', '信息', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
      .then(() => {
        NewscommentDel({id:row.id})
          .then(res => {
            if (res.errcode) {
              this.$message({
                type: 'success',
                message: '删除成功!'
              })
              this.getdata(this.param)
            } else {
              this.$message({
                type: 'info',
                message: res.errmsg
              })
            }
          })
          .catch(err => {
            console.log('err:',err)
            this.loading = false
            this.$message.error('删除失败，请稍后再试！')
          })
      })
      .catch((err) => {
        console.log('err:',err)
        this.$message({
          type: 'info',
          message: '已取消删除'
        })
      })
    },
    // 关闭编辑、增加弹出框
    closeDialog() {
      this.editFormVisible = false
    }
  }
}
</script>

<style scoped>
.user-search {
  margin-top: 20px;
}
.userRole {
  width: 100%;
}
.table_img{
  width: 35px;
  height: 35px;
}
.el-form-item__label{
  text-align:left;
}
.dialog-footer{
  text-align:center;
}
.detail{
  margin:50px
}
.detail-item{
  padding:20px;min-width:100px;
}
.label-h1{
  float:left;display:inline;font-size: 20px;font-weight: bold;
}
.label-text{
  float:left;display:inline;font-size: 20px;
}
</style>

 



