/**
 * 资讯类别管理 
 */
<template>
  <div>
    <!-- 面包屑导航 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>资讯分类管理</el-breadcrumb-item>
    </el-breadcrumb>
    <el-form class="user-search">
      <el-form-item>
        <el-button size="small" type="primary" icon="el-icon-plus" @click="handleAdd()">添加分类</el-button>
      </el-form-item>
    </el-form>
    <!-- 搜索筛选 -->
    <!-- <el-form :inline="true" :model="searchParam" class="user-search">
      <el-form-item label="搜索：">
        <el-input size="small" v-model="searchParam.id" placeholder="输入资讯类别ID"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button size="small" type="primary" icon="el-icon-search" @click="search">搜索</el-button>
        <el-button size="small" type="primary" icon="el-icon-plus" @click="handleEdit()">添加</el-button>
      </el-form-item>
    </el-form> -->
    <!--列表-->
    <el-table size="small" :data="listData" highlight-current-row v-loading="loading" border element-loading-text="拼命加载中" style="width: 80%;">
      <el-table-column sortable prop="id" label="ID" width="100">
      </el-table-column>
      <el-table-column sortable prop="name" label="名称" width="200">
      </el-table-column>
      <el-table-column sortable prop="statusname" label="状态" width="100">
      </el-table-column>
      <el-table-column sortable prop="sort" label="排序值" width="100">
      </el-table-column>
      <el-table-column align="center" label="操作" min-width="300">
        <template slot-scope="scope">
          <el-button size="mini" @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
          <el-button size="mini" type="danger" @click="deleteUser(scope.$index, scope.row)" v-if="scope.row.status==1">禁用</el-button>
          <el-button size="mini" type="danger" @click="deleteUser(scope.$index, scope.row)" v-else>启用</el-button>
        </template>
      </el-table-column>
    </el-table>
    <!-- 分页组件 -->
    <!-- <Pagination v-bind:child-msg="pageparm" @callFather="callFather"></Pagination> -->
    <!-- 编辑界面 -->
    <el-dialog :title="title" :visible.sync="editFormVisible" width="30%" @click="closeDialog">
      <el-form label-width="120px" :model="editForm" :rules="rules" ref="editForm">
        <el-form-item label="ID" prop="id" >
          <el-input size="small" v-model="editForm.id" auto-complete="off" :disabled="true"></el-input>
        </el-form-item>
        <el-form-item label="名称" prop="name">
          <el-input size="small" v-model="editForm.name" auto-complete="off" placeholder="请输入名称"></el-input>
        </el-form-item>
        <el-form-item label="排序值" prop="sort">
          <el-input size="small" v-model="editForm.sort" auto-complete="off" placeholder="请输入排序值"></el-input>
        </el-form-item>
        <el-form-item label="状态" prop="status">
          <el-select size="small" v-model="editForm.status" placeholder="请选择" class="userRole">
            <el-option
            v-for="item in listStatus"
            :key="item.id"
            :label="item.name"
            :value="item.id">
          </el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button size="small" @click="closeDialog">取消</el-button>
        <el-button size="small" type="primary" :loading="loading" class="title" @click="submitEditForm()">保存</el-button>
      </div>
    </el-dialog>
    <!-- 新增界面 -->
    <el-dialog :title="title" :visible.sync="addFormVisible" width="30%" @click="closeDialog">
      <el-form label-width="120px" :model="addForm" :rules="rules" ref="editForm">
        <el-form-item label="名称" prop="name">
          <el-input size="small" v-model="addForm.name" auto-complete="off" placeholder="请输入名称"></el-input>
        </el-form-item>
        <el-form-item label="排序值" prop="sort">
          <el-input size="small" v-model="addForm.sort" auto-complete="off" placeholder="请输入排序值"></el-input>
        </el-form-item>
        <el-form-item label="状态" prop="status">
          <el-select size="small" v-model="addForm.status" placeholder="请选择" class="userRole">
            <el-option
            v-for="item in listStatus"
            :key="item.id"
            :label="item.name"
            :value="item.id">
          </el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button size="small" @click="closeDialog">取消</el-button>
        <el-button size="small" type="primary" :loading="loading" class="title" @click="submitAddForm()">新增</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import {
  NewscateList,
  NewscateDo
} from '../../api/apiConfig'
import Pagination from '../../components/Pagination'
export default {
  data() {
    return {
      nshow: true, //switch开启
      fshow: false, //switch关闭
      loading: false, //是显示加载
      editFormVisible: false, //控制编辑页面显示与隐藏
      addFormVisible: false,
      title: '添加',
      type:0, //1编辑 2添加
      editForm: {
        act:"update",
        id: '',
        name: '',
        status: '',
        sort: '',
        token: localStorage.getItem('logintoken')
      },
      addForm: {
        act:"add",
        name: '',
        status: '',
        sort: '',
        token: localStorage.getItem('logintoken')
      },
      searchParam:{
        act:"get",
        id:""
      },
      // rules表单验证
      rules: {
        name: [
          { required: true, message: '请输入名称', trigger: 'blur' }
        ],
        sort: [
          { required: true, message: '请输入排序值', trigger: 'blur' }
        ],
        status: [{ required: true, message: '请选择状态', trigger: 'blur' }]
      },
      formInline: {},
      // 删除部门
      seletedata: {
        ids: '',
        token: localStorage.getItem('logintoken')
      },
      userparm: [], //搜索权限
      listData: [], //用户数据
      listStatus:[{id:1,name:"正常"},{id:2,name:"禁用"}],
      // 分页参数
      pageparm: {
        currentPage: 1,
        pageSize: 10,
        total: 10
      }
    }
  },
  // 注册组件
  components: {
    Pagination
  },
  /**
   * 创建完毕
   */
  created() {
    this.getdata(this.formInline)
  },

  /**
   * 里面的方法只有被调用才会执行
   */
  methods: {
    // 获取资讯类别列表
    getdata(parameter) {
      this.loading = true
      /***
       * 调用接口
       */
      let status_list = {1:"正常",2:"禁用"}
      NewscateList(parameter)
        .then(res => {
          this.loading = false
          if (res.errcode === 0) {
            let listData  = res.data.catelist;
            for (var i = listData.length - 1; i >= 0; i--) {
              let statusid = listData[i]['status']
              listData[i]['statusname'] = status_list[statusid] ? status_list[statusid] : statusid
            }
            this.listData  = listData
            // console.log("listData:",this.listData)
            // 分页赋值
            // this.pageparm.currentPage = res.data.current_page
          } else {
            this.$message({
              type: 'info',
              message: res.errmsg
            })
          }
        })
        .catch(err => {
          this.loading = false
          this.$message.error('获取数据失败，请稍后再试！')
        })
    },
    // 分页插件事件
    callFather(parm) {
      this.formInline.page = parm.currentPage
      this.formInline.limit = parm.pageSize
      this.getdata(this.formInline)
    },
    // 搜索事件
    search() {
      // console.log("searchParam",this.searchParam)
      this.getdata(this.searchParam)
    },
    //显示编辑界面
    handleEdit: function(index, row) {
      this.editFormVisible = true
      this.title = '修改类别'
      this.type = 1
      this.editForm.id = row.id
      this.editForm.name = row.name
      this.editForm.sort = row.sort
      this.editForm.status = row.status
    },
    // 编辑页面保存方法
    submitEditForm() {
      console.log("editData:",this.editForm)
      NewscateDo(this.editForm)
        .then(res => {
          this.editFormVisible = false
          this.loading = false
          if (res.errcode === 0) {
            this.getdata(this.formInline)
            this.$message({
              type: 'success',
              message: '保存成功！'
            })
          } else {
            this.$message({
              type: 'info',
              message: res.errmsg
            })
          }
        })
        .catch(err => {
          this.editFormVisible = false
          this.loading = false
          this.$message.error('保存失败，请稍后再试！')
        })
    },
     //显示新界面
    handleAdd: function(index, row) {
      this.addFormVisible = true
      this.title = '添加类别'
      this.type = 2
      this.addForm.id = ''
      this.addForm.name = ''
      this.addForm.sort = 1
      this.addForm.status = 1
    },
    // 编辑、增加页面保存方法
    submitAddForm() {
      NewscateDo(this.addForm)
        .then(res => {
          this.addFormVisible = false
          this.loading = false
          if (res.errcode === 0) {
            this.getdata(this.formInline)
            this.$message({
              type: 'success',
              message: '新增成功！'
            })
          } else {
            this.$message({
              type: 'info',
              message: res.errmsg
            })
          }
        })
        .catch(err => {
          this.addFormVisible = false
          this.loading = false
          this.$message.error('保存失败，请稍后再试！')
        })
    },
    // 删除资讯类别
    deleteUser(index, row) {
      let param = {
        act:"status",
        id:row.id
      };
      let actname = '启动用'
      param['status'] = 1 
      if(row.status==1){
        actname = '禁用'
        param['status'] = 2 
      }
      this.$confirm('确定要'+actname+'吗?', '信息', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(() => {
          NewscateDo(param)
            .then(res => {
              if (res.errcode === 0) {
                this.$message({
                  type: 'success',
                  message: '操作成功!'
                })
                this.getdata(this.formInline)
              } else {
                this.$message({
                  type: 'info',
                  message: res.errmsg
                })
              }
            })
            .catch(err => {
              this.loading = false
              this.$message.error('操作失败，请稍后再试！')
            })
        })
        .catch(() => {
          // this.$message({
          //   type: 'info',
          //   message: '已取消删除'
          // })
        })
    },
    // 关闭编辑、增加弹出框
    closeDialog() {
      this.addFormVisible = false
      this.editFormVisible = false
    }
  }
}
</script>

<style scoped>
.user-search {
  margin-top: 20px;
}
.userRole {
  width: 100%;
}
.table_img{
  width: 35px;
  height: 35px;
}
</style>

 



