/**
 * 社区内容管理 
 */
<template>
  <div>
    <!-- 面包屑导航 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>社区内容管理</el-breadcrumb-item>
    </el-breadcrumb>
    <!-- 搜索筛选 -->
    <el-form :inline="true" :model="searchParam" class="user-search">
      <el-form-item label="搜索：">
        <el-input size="small" v-model="searchParam.keywords" placeholder="输入内容关键词"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button size="small" type="primary" icon="el-icon-search" @click="search">搜索</el-button>
        <!-- <el-button size="small" type="primary" icon="el-icon-plus" @click="handleEdit()">添加</el-button> -->
      </el-form-item>
    </el-form>
    <!--列表-->
    <el-table size="small" :data="listData" highlight-current-row v-loading="loading" border element-loading-text="拼命加载中" style="width: 100%;">
      <el-table-column prop="id" label="ID" width="70"></el-table-column>
      <el-table-column prop="title" label="内容" min-width="300">
      </el-table-column>
      <el-table-column prop="audio_video_img" label="视频 || 图片" width="200">
        <template slot-scope="scope">
          <video :src="scope.row.audio_video" controls="controls" v-if="scope.row.audio_video">您的浏览器不支持 video 标签。</video>
          <img class="table_img" :src="scope.row.img" alt="" v-if="scope.row.img" />
        </template>
      </el-table-column>
      <el-table-column prop="author_id" label="作者" width="100"></el-table-column>
      <el-table-column sortable prop="loves" label="点赞量" width="100"></el-table-column>
      <el-table-column sortable prop="views" label="浏览量" width="100"></el-table-column>
      <el-table-column sortable prop="comment" label="评论量" width="100"></el-table-column>
      <el-table-column sortable prop="time_create" label="发布时间" width="140">
        <template slot-scope="scope">
          <div>{{scope.row.time_create|timestampToTime}}</div>
        </template>
      </el-table-column>
      <el-table-column align="center" label="操作" width="180">
        <template slot-scope="scope">
          <el-button size="mini" @click="toComment(scope.$index, scope.row)">查看详情</el-button>
          <el-button size="mini" type="danger" @click="delRecord(scope.$index, scope.row)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
    <!-- 分页组件 -->
    <Pagination v-bind:child-msg="pageparm" @callFather="callFather"></Pagination>
  </div>
</template>

<script>
import {
  talkConfigList,
  communityDo
} from '../../api/apiConfig'
import Pagination from '../../components/Pagination'
export default {
  data() {
    return {
      id:0,
      nshow: true, //switch开启
      fshow: false, //switch关闭
      loading: false, //是显示加载
      editFormVisible: false, //控制编辑页面显示与隐藏
      title: '添加',
      searchParam:{
        act:"get",
        keywords:""
      },
      formInline: {
        "page":"1",
        // "cate_id":"1"//1热门，2最新，3最赞
      },
      // 删除部门
      seletedata: {
        ids: '',
        token: localStorage.getItem('logintoken')
      },
      userparm: [], //搜索权限
      listData: [], //用户数据
      // 分页参数
      pageparm: {
        currentPage: 1,
        pageSize: 5,
        total: 1
      }
    }
  },
  // 注册组件
  components: {
    Pagination
  },
  /**
   * 数据发生改变
   */

  /**
   * 创建完毕
   */
  created() {
    this.getdata(this.formInline)
  },

  /**
   * 里面的方法只有被调用才会执行
   */
  methods: {
    // 获取公司列表
    getdata(parameter) {
      this.loading = true
      /***
       * 调用接口
       */
      parameter.pagesize = this.pageparm.pageSize
      talkConfigList(parameter)
        .then(res => {
          this.loading = false
          if (res.errcode === 0) {
            this.listData = res.data.contentlist;
            // console.log("listData:",this.listData)
            // 分页赋值
            this.pageparm.currentPage = res.data.current_page
            this.pageparm.total = res.data.total_count
          } else {
            this.$message({
              type: 'info',
              message: res.errmsg
            })
          }
        })
        .catch(err => {
          this.loading = false
          this.$message.error('数据加载失败，请稍后再试！')
        })
    },
    // 分页插件事件
    callFather(parm) {
      this.formInline.page = parm.currentPage
      this.formInline.pagesize = parm.pageSize
      this.getdata(this.formInline)
    },
    // 搜索事件
    search() {
      this.getdata(this.searchParam)
    },
    // 删除
    delRecord(index, row) {
      this.$confirm('确定要删除吗?', '信息', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
      .then(() => {
        communityDo({id:row.id,status:'-1'}).then(res => {
            if (res.errcode===0) {
              this.$message({
                type: 'success',
                message: '已删除!'
              })
              this.getdata(this.formInline)
            } else {
              this.$message({
                type: 'info',
                message: res.errmsg
              })
            }
          })
          .catch(err => {
            this.loading = false
            this.$message.error('删除失败，请稍后再试！')
          })
      })
      .catch(() => {
        // this.$message({
        //   type: 'info',
        //   message: '已取消删除'
        // })
      })
    },
    //查看评论
    toComment(index,row){
      // console.log("index:",index)
      // console.log("row:",row)
      this.$router.push({ path: '/talks/talk_detail?id='+row.id})
    }
  }
}
</script>

<style scoped>
.user-search {
  margin-top: 20px;
}
.userRole {
  width: 100%;
}
video{
  width: 100%;
  height: auto;
}
.table_img{
  width: 100%;
  height: auto;
}
</style>

 



