              menus: null
            },
            {
              menuid: 35,
              icon: 'icon-cms-manage',
              menuname: '资讯类别管理',
              hasThird: 'N',
              url: 'News/news_type',
              menus: null
            }
          ]
        },
        {
          menuid: 4,
          icon: 'li-icon-shangchengxitongtubiaozitihuayuanwenjian91',
          menuname: '社区内容管理',
          hasThird: null,
          url: null,
          menus: [
            {
              menuid: 44,
              icon: 'icon-provider-manage',
              menuname: '社区内容',
              hasThird: 'N',
              url: 'talks/talks',
              menus: null
            }
          ]
        },
        {
          menuid: 5,
          icon: 'icon-cus-manage',
          menuname: '用户信息管理',
          hasThird: null,
          url: null,
          menus: [
            {
              menuid: 55,
              icon: 'icon-cus-manage',
              menuname: '管理员信息',
              hasThird: 'N',
              url: 'admin/admin',
              menus: null
            },
            {
              menuid: 56,
              icon: 'icon-cus-manage',
              menuname: '用户信息',
              hasThird: 'N',
              url: 'admin/user',
              menus: null
            }
          ]
        },
        {
          menuid: 1,
          icon: 'li-icon-xiangmuguanli',
          menuname: '系统管理',
          hasThird: null,
          url: null,
          menus: [
