              icon: 'icon-order-manage',
              menuname: '用户设备',
              hasThird: 'N',
              url: 'message/member_device',
              menus: null
            }
          ]
        },
        {
          menuid: 6,
          icon: 'icon-cus-manage',
          menuname: '其他内容管理',
          hasThird: null,
          url: null,
          menus: [
            {
              menuid: 61,
              icon: 'icon-cus-manage',
              menuname: '反馈建议',
              hasThird: 'N',
              url: 'other/feedback',
              menus: null
