/**
 * 管理员管理 
 */
<template>
  <div>
    <!-- 面包屑导航 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item :to="{ path: '/talks/talks' }">社区内容管理</el-breadcrumb-item>
      <el-breadcrumb-item>社区内容详情</el-breadcrumb-item>
    </el-breadcrumb>
    <div style="margin: 50 200 50 50;">
        <div class="detail">
          <el-form label-width="120px" ref="editForm">
            <el-form-item label="内容：">
              <label class="label-text">{{detailData.title}}</label>
            </el-form-item>
            <el-form-item label="图片：">
              <label class="label-text" v-if="detailData.img">
                  <div class="imgli" v-for="item in detailData.img" :key="item">
                    <img :src="item" alt=""/>
                  </div>
              </label>
            </el-form-item>
            <el-form-item label="视频：">
              <div slot-scope="scope" class="video">
                <video :src="detailData.audio_video" controls="controls" v-if="detailData.audio_video">您的浏览器不支持 video 标签。</video>
              </div>
            </el-form-item>
            <el-form-item label="浏览量: ">
              <label class="label-text">{{detailData.views}}</label>
            </el-form-item>

            <el-form-item label="评论量: ">
              <label class="label-text">{{detailData.comment}}</label>
            </el-form-item>
            <el-form-item label="点赞量: ">
              <label class="label-text">{{detailData.loves}}</label>
            </el-form-item>
            <el-form-item label="标签: ">
              <label class="label-text">{{detailData.labels}}</label>
            </el-form-item>
            <el-form-item label="作者: ">
              <label class="label-text">{{detailData.author_name}}</label>
            </el-form-item>
            <el-form-item label="作者头像: ">
              <img :src="detailData.author_headimg" width="80">
            </el-form-item>
            <el-form-item label="评论列表: ">
                <!--列表-->
                <el-table size="small" :data="listData" highlight-current-row v-loading="loading" element-loading-text="拼命加载中" style="width: 100%;">
                  <el-table-column prop="id" label="ID" width="100"></el-table-column>
                  <el-table-column prop="member_name" label="评论者昵称" width="120"></el-table-column>
                  <el-table-column prop="member_heading" label="评论者头像" width="90">
                    <div slot-scope="scope">
                      <img class="table_img" :src="scope.row.member_headimg" alt=""/>
                    </div>
                  </el-table-column>
                  <el-table-column prop="comment" label="评论内容" min-width="200"></el-table-column>
                  <el-table-column prop="comment" label="评论时间" width="150">
                    <template slot-scope="scope">
                      <div>{{scope.row.time_create|timestampToTime}}</div>
                    </template>
                  </el-table-column>
                  <el-table-column align="center" label="操作" width="100">
                    <template slot-scope="scope">
                      <el-button size="mini" type="danger" @click="deleteCommont(scope.$index, scope.row)">删除</el-button>
                    </template>
                  </el-table-column>
                </el-table>
            </el-form-item>
          </el-form>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import {
  commontConfigList,
  detailConfigList,
  CommunitycommentDel
} from '../../api/apiConfig'
import Pagination from '../../components/Pagination'
export default {
  data() {
    return {
      nshow: true, //switch开启
      fshow: false, //switch关闭
      loading: false, //是显示加载
      editFormVisible: false, //控制编辑页面显示与隐藏
      title: '添加',
      detailParam: {id:0},
      detailData: {}, //社区内容详情
      listData: [], //社区内容评论列表
      // 分页参数
      pageparm: {
        currentPage: 1,
        pageSize: 10,
        total: 10
      }
    }
  },
  // 注册组件
  components: {
    Pagination
  },
  /**
   * 数据发生改变
   */

  /**
   * 创建完毕
   */
  created() {
    this.getdata(this.detailParam)
  },
  mounted () {
    this.pageInit();
  },
  /**
   * 里面的方法只有被调用才会执行
   */
  methods: {
    pageInit(){
      let list = this.$route.query;
      this.detailParam.id = list.id;
    },
    // 获取详情
    getdata(parameter) {
      this.loading = true
      /***
       * 调用接口
       */
        detailConfigList(parameter)
        .then(res => {
          this.loading = false
          if (res.errcode === 0) {
            this.detailData = res.data.community;
            this.listData = res.data.commentlist;
          } else {
            this.$message({
              type: 'info',
              message: res.errmsg
            })
          }
        })
        .catch(err => {
          this.loading = false
          this.$message.error('数据加载失败，请稍后再试！')
        })
    },
    // 删除
    deleteCommont(index, row) {
      this.$confirm('确定要删除吗?', '信息', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(() => {
          CommunitycommentDel({id:row.id})
            .then(res => {
              if (res.errcode==0) {
                this.$message({
                  type: 'success',
                  message: '评论已删除!'
                })
                this.getdata(this.detailParam)
              } else {
                this.$message({
                  type: 'info',
                  message: res.errmsg
                })
              }
            })
            .catch(err => {
              this.loading = false
              this.$message.error('删除！')
            })
        })
        .catch(() => {
          // this.$message({
          //   type: 'info',
          //   message: '已取消删除'
          // })
        })
    }
  }
}
</script>

<style scoped>
.user-search {
  margin-top: 20px;
}
.userRole {
  width: 100%;
}
.imgli{
  display: inline-block; 
  margin-right: 10px;
}
.video video{
  max-height: 200px;
}
.imgli img{
  height: 80px;
  max-width:200px;
}
.table_img{
  width: 50px;
  height: 50px;
  border-radius: 50%;
}
.el-form-item__label{
  text-align:left;
}
.dialog-footer{
  text-align:center;
}
.detail{
  margin:50px
}
.detail-item{
  padding:20px;min-width:100px;
}
</style>

 



