/**
 * 资讯管理 资讯配置信息
 */
<template>
  <div>
    <!-- 面包屑导航 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>资讯管理</el-breadcrumb-item>
    </el-breadcrumb>
    <el-form class="user-search">
      <el-button size="small" type="primary" icon="el-icon-plus" @click="handleAdd()">添加资讯</el-button>
    </el-form>
    <!-- 搜索筛选 -->
    <el-form :inline="true" :model="searchParam" class="user-search">
      <el-form-item>
        <el-select v-model="searchParam.cate_id" placeholder="请选择类别">
          <el-option
            v-for="item in listCate"
            :key="item.id"
            :label="item.name"
            :value="item.id">
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="">
        <el-input size="small" v-model="searchParam.keywords" placeholder="输入标题关键词"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button size="small" type="primary" icon="" @click="search">搜索</el-button>
      </el-form-item>
    </el-form>
    <!--列表-->
    <el-table size="small" :data="listData" highlight-current-row v-loading="loading" border element-loading-text="拼命加载中" style="width: 100%;">
      <el-table-column type="index" width="50"></el-table-column>
      <el-table-column prop="id" label="ID" width="50"></el-table-column>
      <el-table-column prop="type" label="类型" width="66"></el-table-column>
      <el-table-column sortable prop="cate_name" label="类别" width="80"></el-table-column>
      <el-table-column sortable prop="title" label="标题" min-width="200"></el-table-column>
      <el-table-column prop="imgs" label="图片" width="120">
        <div  slot-scope="scope">
        <img class="table_img" :src="scope.row.imgs" alt=""/>
        </div>
      </el-table-column>
      <!-- <el-table-column sortable prop="content" label="内容" width="120" :formatter="formatText"></el-table-column> -->
      <el-table-column prop="author_name" label="作者" width="80"></el-table-column>
      <!-- <el-table-column prop="payOpen" label="作者头像" width="70">
        <div  slot-scope="scope">
          <img class="table_img" :src="scope.row.author_headimg" alt=""/>
        </div>
      </el-table-column> -->
      <el-table-column sortable prop="time_create" label="创建时间" width="150">
        <template slot-scope="scope">
          <div>{{scope.row.time_create|timestampToTime}}</div>
        </template>
      </el-table-column>
      <el-table-column sortable prop="statusname" label="状态" width="90"></el-table-column>
      <el-table-column sortable prop="loves" label="点赞量" width="100"></el-table-column>
      <el-table-column sortable prop="views" label="浏览量" width="100"></el-table-column>
      <el-table-column sortable prop="comment" label="评论量" width="100"></el-table-column>
      <el-table-column align="center" label="操作" width="380">
        <template slot-scope="scope">
          <el-button size="mini" @click="toComment(scope.$index, scope.row)">查看评论</el-button>
          <el-button size="mini" type="danger" @click="deleteUser(scope.$index, scope.row.id)">删除</el-button>
          <el-button size="mini" type="primary" @click="upStatus(scope.$index, scope.row)">{{scope.row.status==1?'下架':'上架'}}</el-button>
          <el-button size="mini" @click="handleEdit(scope.$index, scope.row,2)">编辑</el-button>
        </template>
      </el-table-column>
    </el-table>
    <!-- 分页组件 -->
    <Pagination v-bind:child-msg="pageparm" @callFather="callFather"></Pagination>
  </div>
</template>

<script>
import {
  getNewsList,
  getNewsListSave,
  NewsListDelete
} from '../../api/apiConfig'
import Pagination from '../../components/Pagination'
export default {
  data() {
    return {
      nshow: true, //switch开启
      fshow: false, //switch关闭
      loading: false, //是显示加载
      editFormVisible: false, //控制编辑页面显示与隐藏
      title: '添加',
      formInline: {
        // page: 1,
        // limit: 10,
        page:1,
        // cate_id:1,
        token: localStorage.getItem('logintoken')
      },
      searchParam:{
        act:"get",
        keywords:"",
        cate_id: "all"
      },
      userparm: [], //搜索权限
      listData: [], //资讯列表数据
      listCate: [],//类别列表数据
      cateId: 'all',//类别Id
      // 分页参数
      pageparm: {
        currentPage: 1,
        pageSize: 20,
        total: 1
      }
    }
  },
  // 注册组件
  components: {
    Pagination
  },
  /**
   * 数据发生改变
   */

  /**
   * 创建完毕
   */
  created() {
    this.getdata(this.formInline)
  },

  /**
   * 里面的方法只有被调用才会执行
   */
  methods: {
    // 获取资讯列表
    getdata(parameter) {
      let type_list = {1:"文章",2:"音频",3:"视频"}
      let status_list = {1:"上架",2:"下架"}
      this.loading = true
      parameter['pagesize'] = parameter['pagesize'] ? parameter['pagesize'] : this.pageparm['pageSize']
      getNewsList(parameter)
        .then(res => {
          this.loading = false
          //登录过期
          if(res.errcode == 10006){
            this.$store.commit('logout', 'false')
            this.$router.push({ path: '/login' })
            return false
          }
          if (res.errcode === 0) {
            let contentlist = res.data.contentlist
            for (var i = contentlist.length - 1; i >= 0; i--) {
              let typeid = contentlist[i]['type']
              contentlist[i]['type'] = type_list[typeid] ? type_list[typeid] : typeid

              let statusid = contentlist[i]['status']
              contentlist[i]['statusname'] = status_list[statusid] ? status_list[statusid] : statusid
            }
            this.listData = contentlist
            this.listCate = res.data.cate_list
            this.searchParam.cate_id = res.data.cate_id
            // 分页赋值
            this.pageparm.currentPage = res.data.current_page
            this.pageparm.total = res.data.total_count
          } else {
            this.$message({
              type: 'info',
              message: res.errmsg
            })
          }
        })
        .catch(err => {
          this.loading = false
          this.$message.error('数据加载失败，请稍后再试！')
        })
    },
    // 分页插件事件
    callFather(parm) {
      this.formInline.page = parm.currentPage
      this.formInline.pagesize = parm.pageSize
      this.getdata(this.formInline)
    },
    // 搜索事件
    search() {
      this.getdata(this.searchParam)
    },
    //显示编辑界面
    handleEdit: function(index, row,type) {
      this.$router.push({ path: '/News/editNews?id='+row.id})
    },
    handleAdd(type){
      this.$router.push({ path: '/News/editNews'})
    },
    //查看评论
    toComment: function(index, row) {
      // let data = JSON.str this.listData;
      // console.log("row:",row)
      let param = JSON.stringify(row);
      // console.log("this.listData:",this.listData)
      // console.log("param:",param)
      this.$router.push({ path: '/News/news_comment?id='+row.id })
    },
    // 删除资讯
    deleteUser(index, id) {
      // console.log("id：",id)
      let that = this;
      let data = {
        "act":"delete",
        "id":id
      }
      that.$confirm('确定要删除吗?', '信息', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
      .then(() => {
        // console.log("data",data)
        NewsListDelete(data)
          .then(res => {
            if (res.errcode == 0) {
              this.$message({
                type: 'success',
                message: '该资讯已下架成功!'
              })
              this.getdata(this.formInline)
            } else {
              this.$message({
                type: 'info',
                message: res.errmsg
              })
            }
          })
          .catch(err => {
            this.loading = false
            this.$message.error('资讯配置信息下架失败，请稍后再试！')
          })
      })
      .catch(() => {
        // this.$message({
        //   type: 'info',
        //   message: '已取消删除'
        // })
      })
    },
    // 上架下架
    upStatus(index, row) {
      // console.log("row：",row)
      let that = this;
      let data = {
        "act":"status",
        "id":row.id
      }
      data['status'] = 2
      let act =  '下架'
      if( row.status==2 ){
        data['status'] = 1
        act =  '上架'
      }

      that.$confirm('确定要'+act+'吗?', '信息', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(() => {
          // console.log("data",data)
          NewsListDelete(data)
            .then(res => {
              if (res.errcode == 0) {
                this.$message({
                  type: 'success',
                  message: '操作成功!'
                })
                this.getdata(this.formInline)
              } else {
                this.$message({
                  type: 'info',
                  message: res.errmsg
                })
              }
            })
            .catch(err => {
              this.loading = false
              this.$message.error('操作失败，请稍后再试！')
            })
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          })
        })
    },
    //过滤显示内容
    formatText(row, column, cellValue, index){
      let content = this.decodeUnicode(cellValue);
      // console.log("content:",content)
      if(content){
        content = JSON.parse(content)
      }
      let text = content[0].text?content[0].text:content[1].text;
      // console.log("text:",text)
      text = text.slice(0,20)+'...';
      return text;
    },
    decodeUnicode(str) {  
      str = str.replace(/\\/g, "%");  
      return unescape(str);  
    }
  }
}
</script>

<style scoped>
.user-search {
  margin-top: 20px;
}
.userRole {
  width: 100%;
}
.table_img{
  width: 100%;
}
.el-upload-list--picture .el-upload-list__item{
  width:300px;
}
</style>

 



