/**
 * 用户设备 
 */
<template>
  <div>
    <!-- 面包屑导航 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>用户设备列表</el-breadcrumb-item>
    </el-breadcrumb>
    <el-form :inline="true" :model="formInline" class="user-search">
      <el-form-item label="设备：">
        <el-select v-model="formInline.device_type" placeholder="">
          <el-option label="请选择设备类型" value="0"></el-option>
          <el-option label="Android" value="1"></el-option>
          <el-option label="IOS" value="2"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="关键词：">
        <el-input size="small" v-model="formInline.keywords" placeholder="输入member_id"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button size="small" type="primary" icon="el-icon-search" @click="search">搜索</el-button>
      </el-form-item>
    </el-form>
    <!--列表-->
    <el-table size="small" :data="listData" highlight-current-row v-loading="loading" border element-loading-text="拼命加载中" style="width: 100%;">
      <el-table-column  prop="member_id" label="用户ID" width="60"></el-table-column>
      <el-table-column prop="channel" label="渠道" width="80"></el-table-column>
      <el-table-column prop="device_token" label="device token" width="300"></el-table-column>
      <el-table-column prop="device_type" label="设备类型"></el-table-column>
      <el-table-column prop="subscribe" label="订阅状态"></el-table-column>
      <el-table-column prop="time_modify" label="更新时间" width="140">
        <template slot-scope="scope">
          <div>{{scope.row.time_modify|timestampToTime}}</div>
        </template>
      </el-table-column>
      <el-table-column prop="time_create" label="添加时间" width="140">
        <template slot-scope="scope">
          <div>{{scope.row.time_create|timestampToTime}}</div>
        </template>
      </el-table-column>
    </el-table>
    <!-- 分页组件 -->
    <Pagination v-bind:child-msg="pageparm" @callFather="callFather" ></Pagination>
  </div>
</template>

<script>
import {
  getMemberDeviceList
} from '../../api/apiConfig'
import Pagination from '../../components/Pagination'
export default {
  data() {
    return {
      formInline: {
        device_type: "0",
        keywords: "",
        token: localStorage.getItem('logintoken'),
      },
      listData: [],
      loading: false,
      // 分页参数
      pageparm: {
        currentPage: 1,
        pageSize: 20,
        total: 10
      },
      pickerOptions1:{
        disabledDate(time) {
          return time.getTime() < (Date.now()-24*60*60*1000);
        }
      }
    }
  },
  // 注册组件
  components: {
    Pagination
  },
  /**
   * 数据发生改变
   */

  /**
   * 创建完毕
   */
  created() {
    this.getdata(this.formInline)
  },

  /**
   * 里面的方法只有被调用才会执行
   */
  methods: {
    // 获取管理员列表
    getdata(parameter) {
      this.loading = true
      /***
       * 调用接口
       */
      getMemberDeviceList(parameter)
        .then(res => {
          this.loading = false
          //登录过期
          if(res.errcode == 10006){
            this.$store.commit('logout', 'false')
            this.$router.push({ path: '/login' })
            return false
          }
          if (res.errcode === 0) {
            let data = res.data.datalist;
            /*Object.keys(data).forEach(function (key) {
              data[key]['action'] = '编辑'
            })*/
             this.listData = data
            // 分页赋值
            this.pageparm.currentPage = res.data.current_page
            this.pageparm.total = res.data.total_count
          } else {
            this.$message({
              type: 'info',
              message: res.errmsg
            })
          }
        })
        .catch(err => {
          this.loading = false
          console.log('err:',err)
          this.$message.error('数据加载失败，请稍后再试！')
        })
    },
    // 分页插件事件
    callFather(parm) {
      this.formInline.page = parm.currentPage
      this.formInline.pagesize = parm.pageSize
      this.getdata(this.formInline)
    },
    search(){
      this.getdata(this.formInline)
    },
    cancelForm(){
      this.dialogType['addMessage'] = false
      this.dialogType['editMessage'] = false
    },
    submitForm(formName, type) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          if (type == 'addMessage') {
            //创建
            let ajaxData = this.ruleForm['addMessage']
            ajaxData['act'] = 'add'
            //数据校验

            messagePushDo(ajaxData).then(res => {
              if (res.errcode===0) {
                this.$message({
                  type: 'success',
                  message: res.errmsg
                })
                this.getdata(this.formInline)
              } else {
                this.$message({
                  type: 'info',
                  message: res.errmsg
                })
              }
            })
            .catch(err => {
              this.loading = false
              this.$message.error('操作，请稍后再试！')
            })
          }
          if (type == 'editMessage') {
            //编辑
            let ajaxData = this.ruleForm['editMessage']
            ajaxData['act'] = 'update'
            messagePushDo(ajaxData).then(res => {
              if (res.errcode===0) {
                this.$message({
                  type: 'success',
                  message: res.errmsg
                })
                this.getdata(this.formInline)
              } else {
                this.$message({
                  type: 'info',
                  message: res.errmsg
                })
              }
            })
            .catch(err => {
              this.loading = false
              this.$message.error('操作，请稍后再试！')
            })
          }
          this.dialogType[type] = false
        } else {
          console.log('error submit!!');
          return false;
        }
      });
    },
  }
}
</script>

<style scoped>
.user-search {
  margin-top: 20px;
}
.userRole {
  width: 100%;
}
.table_img{
  width: 35px;
  height: 35px;
}
</style>

 



